.about {
	margin-bottom: $outter-margin;
	&-img {
		margin-bottom: 40px;
	}
	@include _mobile {
		margin-bottom: $outter-margin_mobile;
		&-img {
			margin-bottom: #{$inner-margin - 18px};
		}
	}
	@include _tablet {
		margin-bottom: $outter-margin_tablet;
		&-img {
			margin-bottom: #{$inner-margin - 18px};
		}
		p {
			margin-bottom: 20px;
		}
	}
}
