.submenu-items {
	@extend %ul-reset;
	display: grid;
	grid-template-columns: repeat(6, 1fr);
	grid-template-rows: 200px;
	grid-column-gap: 20px;
	grid-row-gap: 20px;
	background-color: #fff;
	width: 100%;
	max-width: 1170px;
	height: 100%;
	overflow-y: auto;
	padding: 25px 50px 40px 50px;
	opacity: 0;
	border-radius: 0 25px 25px 0;
	visibility: hidden;
	display: none;
	&__card {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-direction: column;
		white-space: pre-wrap;
		width: 100%;
		height: 100%;
		padding: 10px;
		position: relative;
		z-index: 1;
		&:after {
			content: '';
			background-color: #ffffff;
			box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05);
			border-radius: 25px;
			left: 0;
			height: calc(100% + 20px);
			width: 100%;
			position: absolute;
			display: block;
			top: -10px;
			z-index: -1;
			opacity: 0;
			visibility: hidden;
			transition: 0.3s all;
		}

		&:hover,
		&:focus {
			&:after {
				opacity: 1;
				visibility: visible;
			}
		}

		a {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			flex-direction: column;
			text-decoration: none;
			width: 100%;
			height: 100%;
		}
	}
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 87px;
		height: 114px;
		margin-bottom: 10px;
		img {
			width: 100%;
			height: 100%;
		}
	}
	&__title {
		display: flex;
		font-weight: normal;
		flex-grow: 1;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		color: #000000;
		max-height: 48px;
		overflow: hidden;
	}

	&::-webkit-scrollbar-thumb {
		border-radius: 5px;
		width: 6px;
		background: linear-gradient(266.36deg, #f86476 0%, #fea185 34.16%);
	}
	&::-webkit-scrollbar {
		border-radius: 5px;
		background-color: $white;
		width: 6px;
	}

	@include _mobile-tablet {
		display: none !important;
	}

	@include _laptop-sm {
		max-width: 740px;
		grid-template-columns: repeat(4, 1fr);
		padding: 20px 30px 20px 30px;
	}

	@include _laptop {
		max-width: 780px;
		grid-template-columns: repeat(4, 1fr);
		padding: 20px 30px 20px 30px;
	}

	@media (min-width: 1600px) {
		padding: 25px 50px 40px 50px;
	}
}
.submenu-items.active {
	opacity: 1;
	visibility: visible;
	display: grid;
}
