.discount-card {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	text-align: left;
	&__img {
		margin-right: 10px;
		min-width: 175px;
		img {
			transition: 0.3s all;
		}
	}
	&__title {
		font-weight: bold;
		font-size: 24px;
		line-height: 1.05;
		color: #000000;
		margin-bottom: 10px;
	}
	&__description {
		margin-top: 20px;
	}
	&__price {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 15px;
		.old {
			@extend %old-price;
			margin-right: 10px;
		}
		.current {
			@extend %current-price;
		}
		.percent {
			@extend %percent;
			margin-left: auto;
		}
	}
	&__text {
		font-weight: normal;
		font-size: 16px;
		line-height: 1.1;
		color: $grey2;
	}

	@include _mobile {
		margin-bottom: $inner-margin;
		&__img {
			min-width: 125px;
		}
		&__price {
			margin-bottom: #{$inner-margin - 20px};
			.percent {
				display: none;
			}
		}
		&__title {
			@include adaptiv-font(24, 20);
		}
		&__text {
			@include adaptiv-font(16, 14);
		}
	}
	@include _tablet {
		flex-direction: column;
		max-width: 270px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: $inner-margin;
		&__img {
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 50px;
		}
		&__price {
			justify-content: space-between;
			.percent {
				margin-left: 0;
			}
		}
	}

	@include _laptop-sm {
		&__img {
			min-width: 120px;
		}
		&__price {
			.old {
				margin-right: 10px;
			}
			.percent {
				display: none !important;
			}
		}
	}

	@include _laptop {
		&__price {
			flex-wrap: wrap;
			.percent {
				margin-left: 0;
			}

			.current {
				margin-right: 10px;
			}
		}
	}

	@media (min-width: 1024px) {
		&:hover,
		&:focus {
			.discount-card__img {
				img {
					transform: scale(1.1);
				}
			}
			.discount-card__title {
				text-decoration: underline;
			}
		}
	}
}
