@for $i from 1 to 6 {
	h#{$i} {
		@extend %title#{$i};
	}
}
//titles by class
.title {
	@for $i from 1 to 8 {
		&-h#{$i} {
			@extend %title#{$i};
		}
	}
	&_thin {
		font-weight: 300;
	}
}

@for $i from 1 to 4 {
	.subtitle-#{$i} {
		@extend %subtitle#{$i};
	}
}

.section-title {
	@extend %title3;
	text-transform: uppercase;
	color: #000;
	margin-bottom: $inner-margin;
	@include _mobile-tablet {
		text-align: center;
	}
}

.page-title {
	@extend %title3;
	margin-top: 0;
	margin-bottom: $inner-margin;
	text-transform: uppercase;
	text-align: center;

	@include _tablet {
		margin-bottom: #{$inner-margin - 10px};
	}
}
