.category-card_center {
	align-items: center;
	justify-content: flex-start;
	padding: 15px 30px;
	background-color: #fff;
	box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05);
	border-radius: 25px;
	margin-bottom: 30px;
	.category-card__img {
		height: 118px;
		img {
			height: 100%;
		}
	}
	&:hover {
		.category-card__img img {
			transform: scale(1.1);
		}
		.category-card__title {
			text-decoration: underline;
		}
	}

	@include _mobile {
		flex-direction: column;
		padding: 10px 15px;
		margin-bottom: #{$inner-margin - 10px};
		.category-card__img {
			height: 80px;
		}
	}
}
