//
// subtitles

%subtitle1 {
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;
	color: #000000;
}
%subtitle2 {
	font-weight: 300;
	font-size: 16px;
	line-height: 19px;
	color: #000000;
}
%subtitle3 {
	font-weight: 600;
	font-size: 18px;
	line-height: 1.2;

	@include _mobile {
		@include adaptiv-font(18, 14);
	}
