//
// text

%sticker-text {
	font-weight: bold;
	font-size: 12px;
	line-height: 1.15;
	text-align: center;
	text-transform: uppercase;
	@include _mobile {
		@include adaptiv-font(12, 10);
	}
}

%main-text {
	font-size: 16px;
	line-height: 19px;
	color: #000000;
}

%secondary-text {
	font-weight: 300;
	font-size: 14px;
	line-height: 15px;
	letter-spacing: 0.007px;
	color: #000000;
}

%input-text {
	font-weight: 600;
	font-size: 16px;
	line-height: 19px;
}

// old-price
%old-price {
	font-weight: normal;
	font-size: 18px;
	line-height: 1.2;
	text-align: center;
	color: $red2;
	position: relative;
	&::after {
		content: '';
		width: 100%;
		height: 1px;
		background-color: $red2;
		position: absolute;
		left: 0;
		top: 50%;
		transform: skew(7deg, 7deg);
	}
	@include _mobile {
		@include adaptiv-font(18, 12);
	}
}

// current price
%current-price {
	font-weight: 300;
	font-size: 25px;
	line-height: 1.2;
	text-align: center;
	color: #000000;

	@include _mobile {
		@include adaptiv-font(25, 20);
	}
}

// percent
%percent {
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;
	color: $red2;
}

//
%ul-reset {
	padding: 0;
	margin: 0;
	list-style: none;
}

%ol-main {
	@extend %ul-reset;
	margin-bottom: $inner-margin;
	counter-reset: counter;
	li {
		font-weight: 300;
		font-size: 16px;
		line-height: 19px;
		color: #000000;
		margin-bottom: #{$inner-margin / 2};
		&:before {
			counter-increment: counter;
			content: counter(counter) '. ';
		}
		&:last-child {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		li {
			@include adaptiv-font(16, 14);
		}
	}
}

%ul-main {
	@extend %ul-reset;
	margin-bottom: $inner-margin;

	li {
		font-weight: 300;
		font-size: 16px;
		line-height: 1.2;
		color: #000000;
		margin-bottom: #{$inner-margin / 2};
		position: relative;
		padding-left: 15px;
		&:before {
			content: '';
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background-color: #000;
			position: absolute;
			top: 6px;
			left: 0;
		}
	}

	@include _mobile {
		li {
			@include adaptiv-font(16, 14);
		}
	}
}

%ul-secondary {
	@extend %ul-main;
	margin-bottom: 0;
	li {
		position: relative;
		font-weight: 300;
		font-size: 14px;
		line-height: 15px;
		letter-spacing: 0.007px;
		color: #000000;
		padding-left: 15px;
		margin-bottom: #{$inner-margin - 20px};
		&::before {
			content: '';
			left: 0;
			top: 5px;
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background-color: $blue;
		}
	}
}
