.article {
	padding-bottom: $outter-margin;
	margin-bottom: $outter-margin;
	position: relative;
	&::after {
		content: '';
		position: absolute;
		width: 100%;
		max-width: 1475px;
		margin-left: auto;
		margin-right: auto;
		height: 1px;
		background-color: $white;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
	}
	&__img {
		margin-bottom: $inner-margin;
		border-radius: 10px;
		&_no-margin {
			margin-bottom: 0;
		}
	}
	&__gallery {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: $inner-margin;
		img {
			margin-right: 5px;
			border-radius: 10px;
			overflow: hidden;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	@include _mobile {
		padding-bottom: 0;
		margin-bottom: $outter-margin_mobile;
		&::after {
			content: none;
		}
		&__img {
			margin-bottom: #{$inner-margin - 10px};
			&_no-margin {
				margin-bottom: #{$inner-margin - 10px};
			}
		}

		&__gallery {
			flex-direction: column;
			img {
				margin-bottom: 20px;
				margin-right: 0;
			}
		}
		.page-title {
			@include adaptiv-font(40, 26);
		}
	}
	@include _tablet {
		padding-bottom: $outter-margin_tablet;
		margin-bottom: $outter-margin_tablet;
	}
}

@import 'article-heading';
