.contacts {
	margin-bottom: $outter-margin;
	&__map {
		height: 500px;
		width: 100%;
		margin-top: 30px;
		iframe {
			width: 100%;
			height: 100%;
		}
	}
	@include _mobile {
		margin-bottom: 0;
		&__map {
			margin-top: 0;
			margin-bottom: 0;
		}
	}
	@include _tablet {
		margin-bottom: #{$inner-margin - 20px};
		&__map {
			margin-bottom: #{$inner-margin + 20px};
			margin-top: 0;
		}
	}
}

@import 'contacts-item';
