.description-options {
	margin-bottom: $inner-margin;
	border-bottom: 1px solid $white;
	&__title {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		color: #000000;
		margin-bottom: #{$inner-margin / 2};
	}
	&__row {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-wrap: wrap;
		margin-bottom: #{$inner-margin - 10px};
	}
	&__item {
		display: block;
		width: 52px;
		height: 25px;
		margin-right: 20px;
		margin-bottom: 10px;
		border-radius: 5px;
	}
	&__item.active {
		border: 3px solid $blue2;
	}
	&__color {
		&:last-child {
			margin-right: 0;
		}
		&:nth-child(5n + 5) {
			margin-right: 0;
		}
	}
	&__value {
		border: 1px solid $grey3;
		box-sizing: border-box;
		border-radius: 5px;
		padding: 9px 20px;
		cursor: pointer;
		font-weight: 300;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		transition: 0.3s all;
		text-decoration: none;
	}
	&__value,
	.checkbox-value {
		margin-right: 20px;
		margin-bottom: 10px;
	}
	.checkbox-value.last,
	&__value.last {
		margin-right: 0;
	}

	&__value.active {
		font-weight: bold;
		color: #fff;
		background-color: $blue;
	}

	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 20px 40px;
		background-color: #fff;
		box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05);
		border-radius: 25px;
	}

	@include _mobile {
		&__img {
			max-width: 320px;
			height: 100px;
			margin-bottom: #{$inner-margin - 20px};
			margin-left: auto;
			margin-right: auto;
			img {
				height: 100%;
			}
		}
	}

	@include _tablet {
		&__value,
		.checkbox-value {
			margin-right: 10px;
			margin-bottom: 10px;
		}
	}
	@include _laptop-sm {
		&__value,
		.checkbox-value {
			margin-right: 10px;
			margin-bottom: 10px;
		}
	}
	@include _laptop {
		&__value,
		.checkbox-value {
			margin-right: 10px;
			margin-bottom: 10px;
		}
	}
}
