.history-card {
	background-color: #f3f3f3;
	border-radius: 10px;
	font-size: 16px;
	line-height: 1.2;
	color: #000000;
	padding: 14px 18px;
	display: flex;
	flex-grow: 1;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-bottom: #{$inner-margin - 10px};
	&__img {
		width: 20%;
	}
	&__title {
		width: 40%;
		text-transform: uppercase;
		text-decoration: none;
		color: #000;
	}
	&__quantity {
		width: 10%;
		text-align: center;
	}
	&__price {
		width: 30%;
		text-align: center;
		font-size: 24px;
		line-height: 29px;
		text-transform: uppercase;
	}
	&__date {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		&:after {
			content: '';
			width: 1px;
			height: 40px;
			background-color: #000;
			display: inline-block;
			margin-left: 15px;
			margin-right: 15px;
		}
	}
	&__status {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		// flex-wrap: ;
		&_green strong {
			color: $green;
		}
		&_red strong {
			color: $red3;
		}
		&_yellow strong {
			color: $yellow2;
		}
		&_blue strong {
			color: $blue;
		}
	}

	&__bottom {
		margin-top: #{$inner-margin - 15px};
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}

	.caption {
		margin-right: 5px;
	}

	@include _mobile {
		@include adaptiv-font(16, 14);
		&__img {
			width: 15%;
		}
		&__title {
			width: 40%;
		}
		&__quantity {
			width: 15%;
		}
		&__date {
			&::after {
				margin-left: 10px;
				margin-right: 10px;
			}
		}

		&__bottom {
			justify-content: flex-start;
		}
	}
}
