.header-cart-card {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 15px 20px 15px 30px;
	border-bottom: 1px solid $white;
	position: relative;
	&__img {
		width: 50px;
		min-width: 50px;
		height: 65px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__title,
	&__quantity {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		text-transform: uppercase;
		color: #000000;
		text-decoration: none;
		max-width: 120px;
	}
	&__price {
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		text-transform: uppercase;
		color: #000000;
	}
	.btn-delete {
		position: absolute;
		width: 8px;
		height: 8px;
		display: flex;
		align-items: center;
		justify-content: center;
		top: 10px;
		right: 10px;
		cursor: pointer;
	}
}
