.header-mobile {
	position: absolute;
	display: none;
	overflow-x: auto;

	@include _mobile-tablet {
		display: flex;
		flex-direction: column;
		position: fixed;
		z-index: 999;
		background-color: #fff;
		top: 0;
		left: 0;
		// width: 430px;
		height: 100%;
		opacity: 0;
		visibility: hidden;
		transform: translateX(-200%);
		transition: 0.3s all;
		&.active {
			opacity: 1;
			visibility: visible;
			transform: translateX(0);
		}
		&__heading {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 20px 20px 20px 50px;
		}
		&__btn {
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				width: 19px;
				height: 19px;
			}
		}
		&__categories-toggle {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			padding-left: 50px;
			margin-bottom: 20px;
			&-icon {
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: 25px;
				content: '';
				width: 45px;
				height: 45px;
				border-radius: 50%;
				background-color: #f5f5f5;
				svg {
					width: 24px;
					height: 24px;
				}
			}

			span {
			}
		}
	}
	@include _mobile {
		width: 100%;
		&__categories-toggle {
			padding-left: 15px;
		}
		.header-actions__item {
			display: none;
		}
	}
	@include _tablet {
		width: 430px;
	}
}

.header-mobile-login {
	padding: 18px 50px;
	background-color: $blue;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	margin-bottom: 30px;
	&__icon {
		width: 45px;
		min-width: 45px;
		height: 45px;
		margin-right: 18px;
		background-color: #fff;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		svg {
			width: 26px;
			height: 24px;
			color: $blue;
		}
	}
	&__text {
		p {
			color: #fff;
			font-size: 14px;
			line-height: 17px;
			font-weight: normal;
			b {
				font-weight: bold;
			}
		}
		a {
			font-weight: 300;
			font-size: 18px;
			line-height: 22px;
			color: #ffffff;
			text-decoration: none;
			&:first-child {
				padding-right: 20px;
				margin-right: 10px;
				border-right: 1px solid #fff;
			}
		}
	}

	@include _mobile {
		padding: 15px;
	}
}

.header-mobile-actions {
	padding-bottom: 20px;
	border-bottom: 1px solid #dddddd;
	margin-bottom: 25px;
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding: 10px 50px;
		text-decoration: none;
		span {
			color: #000;
			font-weight: normal;
			font-size: 18px;
			line-height: 22px;
		}
	}
	&__item-icon {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 45px;
		height: 45px;
		border-radius: 50%;
		background-color: #f5f5f5;
		margin-right: 20px;
		svg {
			width: 24px;
			height: 24px;
			color: #7b7b7b;
		}
	}

	@include _mobile {
		&__item {
			padding: 15px;
		}
	}
}

.header-mobile-options {
	padding-left: 50px;
	border-bottom: 1px solid #dddddd;
	margin-bottom: 20px;
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 20px;
		.caption {
			font-weight: normal;
			font-size: 18px;
			line-height: 22px;
			color: #5a5a5a;
			margin-right: 40px;
		}
		.dropdown {
			&__toggle {
			}
		}
		.switcher {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			&__item {
				margin-right: 5px;
				padding-right: 5px;
				border-right: 1px solid #dddddd;
				font-weight: 300;
				font-size: 18px;
				line-height: 22px;
				color: #ddd;
				&.active {
					font-weight: 600;
					color: #000;
				}
				&:last-child {
					border-right: none;
				}
			}
		}
	}

	@include _mobile {
		padding-left: 15px;
	}
}

.header-mobile {
	&__contacts {
		padding-left: 50px;
		border-bottom: 1px solid #ddd;
		margin-bottom: 25px;
	}
	&__contacts-item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 25px;
		}
		svg {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 20px;
			height: 20px;
			margin-right: 10px;
		}
	}
	&__social {
		padding-left: 50px;
	}
	&__social-wrapper {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	&__social-item {
		display: flex;
		align-items: center;
		justify-content: center;
		text-decoration: none;
		width: 28px;
		height: 28px;
		margin-right: 10px;
	}

	.title-h5 {
		margin-bottom: 15px;
	}

	@include _mobile {
		&__contacts {
			padding-left: 15px;
		}
		&__social {
			padding-left: 15px;
			margin-bottom: 30px;
		}
	}
}

.header-mobile {
	&__catalog {
		position: fixed;
		width: 430px;
		left: 0;
		top: 74px;
		z-index: 222;
		background-color: #fff;
		height: 100%;
		overflow: auto;
		transform: translateX(-200%);
		transition: 0.3s all;
		&.active {
			transform: translateX(0);
		}
	}
	&__catalog-back {
		padding: 20px 20px 20px 50px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		border-top: 1px solid #ddd;
		border-bottom: 1px solid #ddd;
		margin-bottom: 15px;
		span {
			font-weight: bold;
			font-size: 18px;
			line-height: 22px;
			color: #000000;
		}
	}
	&__catalog-back-icon {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 45px;
		height: 45px;
		border-radius: 50%;
		background-color: $blue;
		margin-right: 15px;
		svg {
			width: 24px;
			height: 24px;
		}
	}
	.btn-back-to-menu {
		margin-left: auto;
		svg {
			width: 12px;
			height: 12px;
		}
	}

	@include _mobile {
		&__catalog {
			width: 100%;
		}
		&__catalog-back {
			padding-left: 15px;
		}
	}
}
