.blog-card {
	display: flex;
	align-items: stretch;
	justify-content: flex-start;
	background-color: #ffffff;
	box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
	border-radius: 25px;
	&__img {
		width: 220px;
		min-width: 220px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		height: auto;
		overflow: hidden;
		border-radius: 25px 0 0 25px;
		img {
			height: 100%;
			width: 100%;
			object-fit: cover;
			border-radius: 25px 0 0 25px;
		}
	}
	&__description {
		padding: 30px;
	}
	&__title {
		display: block;
		margin-bottom: 20px;
		font-weight: 800;
		font-size: 30px;
		line-height: 1.14;
		color: #000000;
		text-decoration: none;
		max-height: 68px;
		overflow: hidden;
	}
	&__text {
		margin-bottom: 20px;
		font-weight: 300;
		font-size: 16px;
		line-height: 1.2;
		color: #000000;
		max-height: 76px;
		overflow: hidden;
	}
	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-height: 24px;
		overflow: hidden;
	}
	&__read-more {
		text-decoration: none;
		span {
			font-weight: normal;
			font-size: 20px;
			line-height: 1.2;
			text-align: center;
			color: $grey1;
			transition: 0.3s all;
		}

		svg {
			width: 12px;
			height: 12px;
			color: $grey1;
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			span {
				color: $orange;
			}
			svg {
				transform: translateX(5px);
				color: $orange;
			}
		}
	}
	&__date {
		font-weight: bold;
		font-size: 16px;
		line-height: 1.2;
		color: $grey1;
	}
	@include _mobile {
		flex-direction: column;
		max-width: 420px;
		margin-left: auto;
		margin-right: auto;
		&__img {
			max-width: none;
			display: block;
			width: 100%;
			border-radius: 25px 25px 0 0;
			max-height: 170px;
			img {
				border-radius: 25px 25px 0 0;
				width: 100% !important;
				object-fit: cover;
			}
		}
		&__description {
			padding: 20px;
		}
		&__title {
			@include adaptiv-font(30, 20);
			max-height: none;
		}
		&__text {
			@include adaptiv-font(16, 14);
			max-height: none;
		}
		&__bottom {
			max-height: none;
		}

		&__read-more {
			span {
				@include adaptiv-font(20, 14);
			}
			svg {
				display: none;
			}
		}

		&__date {
			@include adaptiv-font(16, 14);
		}
	}
	@include _tablet {
	}
	@include _laptop-sm {
		max-width: 900px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
	}
	@media (min-width: 1024px) {
		&:hover & {
			&__title {
				text-decoration: underline;
			}
		}
	}
}
