.quantity {
	border: 1px solid $white;
	border-radius: 50px;
	width: fit-content;

	input {
		border: none;
		border-left: 1px solid $white;
		border-right: 1px solid $white;
		background-color: transparent;
	}
	&_sm {
		height: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 3px 0;
		input {
			height: 100%;
			width: 30px;
		}
		input,
		button {
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
			text-align: center;
			color: #000000;
		}

		button {
			width: 25px;
			transition: 0.3s all;
			&:hover {
				color: $orange;
			}
		}
	}

	&_bg {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 50px;
		padding: 5px 0;
		input {
			height: 100%;
			width: 44px;
		}
		input,
		button {
			font-weight: bold;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			color: #000000;
		}
		button {
			width: 38px;
			transition: 0.3s all;
			&:hover {
				color: $orange;
			}
		}
	}
	button {
		height: 100%;
	}
}
