.description-offer {
	margin-bottom: $inner-margin;
	.title-h5 {
		margin-bottom: #{$inner-margin - 10px};
	}

	@include _tablet {
		margin-bottom: #{$inner-margin - 15px};
	}
}

.description-offer-card {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	background-color: $white;
	border-radius: 10px;
	padding: 8px 110px 13px 60px;
	margin-right: 50px;
	width: 100%;
	&__img {
		height: 140px;
		min-width: 110px;
		display: block;
		width: auto;
		margin-right: 45px;
		img {
			height: 100%;
			width: auto;
		}
	}

	&__title {
		display: block;
		font-weight: normal;
		font-size: 20px;
		line-height: 1.2;
		color: #000000;
		margin-bottom: #{$inner-margin - 20px};
		text-decoration: none;
	}

	.old-price {
		font-weight: normal;
		font-size: 18px;
		line-height: 22px;
		text-transform: uppercase;
		color: $red2;
		position: relative;
		width: fit-content;
		&::before {
			content: '';
			width: 100%;
			height: 1px;
			position: absolute;
			background-color: red;
			left: 0;
			top: 50%;
			transform: translateY(-50%) skew(7deg, 7deg);
		}
	}
	.current-price {
		font-weight: bold;
		font-size: 30px;
		line-height: 37px;
		color: #000000;
	}
	.caption {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		color: $grey2;
	}

	@include _mobile {
		padding: 10px 5px 30px 20px;
		position: relative;
		z-index: 1;
		overflow: hidden;
		max-width: 400px;
		&__img {
			position: absolute;
			top: 10px;
			left: 10px;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 120px;
			z-index: -1;
		}
		.caption {
			display: none;
		}
		&__title {
			@include adaptiv-font(20, 16);
			margin-bottom: 50px;
			max-width: 150px;
			margin-left: auto;
			min-height: 76px;
		}
	}
}
