.account {
	.container {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
	}
	&_no-autorization {
		margin: 200px 0;
	}
	&_autorization {
		margin: 100px 0;
	}
	&_no-bottom-margin {
		margin-bottom: 0;
	}
	.row_scroll {
		height: 400px;
		overflow-y: auto;
		&::-webkit-scrollbar-thumb {
			border-radius: 5px;
			width: 6px;
			background: linear-gradient(266.36deg, #f86476 0%, #fea185 34.16%);
		}
		&::-webkit-scrollbar {
			border-radius: 5px;
			background-color: $white;
			width: 6px;
		}
	}

	@include _mobile {
		&_no-autorization {
			margin: 75px 0;
		}
		&_autorization {
			margin: 40px 0;
		}
	}

	@include _tablet {
		&_no-autorization {
			margin: 190px 0;
		}
		&_autorization {
			margin: 30px 0 80px 0;
		}
	}
}

@import 'account-form';
@import 'account-heading';
@import 'account-history';
@import 'account-reviews';
