.description-timer {
	margin-bottom: $inner-margin;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding: 13px 23px 13px 103px;
	background-color: $blue3;
	background-image: url(../img/timer-pattern.svg);
	background-repeat: repeat;
	background-size: 46px 44px;
	position: relative;
	min-height: 97px;
	border-radius: 10px;
	z-index: 1;
	&__img {
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		height: 100%;
		img {
			height: 100%;
		}
	}
	.title-h4 {
		max-width: 170px;
		line-height: 29px;
	}
	.subtitle-3 {
		padding-left: 15px;
		border-left: 1px solid #000;
		max-width: 180px;
	}
	&__timer {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}

	&__item {
		margin-right: 30px;
		position: relative;
		&::after,
		&:before {
			content: '';
			width: 7px;
			height: 7px;
			border-radius: 50%;
			background-color: #fff;
			position: absolute;
			right: -18.5px;
		}
		&:after {
			bottom: 35px;
		}
		&:before {
			top: 15px;
		}
		&:last-child {
			margin-right: 0;
			&::after,
			&::before {
				content: none;
			}
		}
	}
	&__value {
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: 800;
		font-size: 40px;
		line-height: 49px;
		text-align: center;
		text-transform: uppercase;
		color: #000000;
		background-color: #fff;
		box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.1);
		border-radius: 10px;
		padding: 2px;
		min-width: 57px;
	}
	&__caption {
		margin-top: 5px;
		font-weight: bold;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		color: #000000;
	}

	@include _mobile {
		flex-wrap: wrap;
		padding: 25px;
		justify-content: flex-start;
		align-items: flex-end;
		// padding-left: 90px;
		overflow: hidden;
		max-width: 400px;
		flex-direction: column;
		.subtitle-3 {
			padding-left: 0;
			padding-top: 10px;
			border-left: none;
			position: relative;
			@include adaptiv-font(18, 16);
			&::after {
				content: '';
				width: 40px;
				height: 1px;
				background-color: #000;
				position: absolute;
				display: block;
				top: 5px;
				left: 0;
			}
		}
		.title-h4,
		.subtitle-3 {
			max-width: 130px;
			width: 100%;
		}
		&__img {
			// display: none;
			height: 150px;
			left: 10px;
			top: 10px;
		}
		&__timer {
			margin-top: 30px;
		}
	}

	@include _tablet {
		&__img {
		}
	}
	@include _laptop {
		flex-wrap: wrap;
		justify-content: flex-end;

		&__timer {
			margin-left: auto;
			margin-right: auto;
		}
		.title-h4,
		.subtitle-3 {
			// max-width: none;
			margin-bottom: 10px;
		}
	}
}
