.sort {
	display: flex;
	align-items: center;
	justify-content: space-between;
	&__dropdown {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
		// flex-wrap: wrap;
	}
	a {
		font-weight: normal;
		font-size: 16px;
		line-height: 17px;
		text-align: center;
		color: $grey2;
		text-decoration: none;
		display: flex;
		margin-right: 60px;
		&:last-child {
			margin-right: 0;
		}
		&:hover,
		&:focus {
			color: rgba(123, 123, 123, 0);
			background: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
			background-clip: text;
			color: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
		}
	}
	&__toggle {
		display: none;
	}
	&__caption {
		display: none;
	}
	@include _mobile-tablet {
		margin-left: auto;
		position: relative;
		&__dropdown {
			flex-direction: column;
			position: absolute;
			opacity: 0;
			visibility: hidden;
			justify-content: flex-end;
			top: 100%;
			z-index: 2;
			right: 0;
			padding: 10px;
			background-color: #fff;
			border-radius: 10px;
			transition: 0.3s all;
			box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
			&.active {
				opacity: 1;
				visibility: visible;
			}
		}
		a {
			margin-right: 0;
		}
		&__toggle {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			font-weight: bold;
			font-size: 16px;
			line-height: 1.05;
			text-align: center;
			color: #000000;
			svg {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 5px;
				height: 11px;
				margin-left: 10px;
				transform: rotate(90deg);
				transition: 0.3s all;
			}

			&.active {
				svg {
					transform: rotate(-90deg);
				}
			}
		}
		&__caption {
			display: flex;
			margin-right: 15px;
		}
	}

	@include _mobile {
		margin-left: 0;
		width: 100%;
		justify-content: space-between;
	}
}
