.price {
	&__main {
		width: 140px;
	}
	&__main-current {
		font-weight: 800;
		font-size: 50px;
		line-height: 73px;
		text-transform: uppercase;
		color: $blue;
		span {
			font-weight: bold;
			font-size: 30px;
			line-height: 37px;
		}
	}
	&__main-caption {
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		color: $grey2;
	}
	&__discount {
		width: 50px;
		font-weight: normal;
		font-size: 18px;
		line-height: 22px;
		text-transform: uppercase;
		color: $red2;
		margin-right: 15px;
	}
	&__discount-percent {
		font-weight: 600;
	}
	&__discount-old-price {
		position: relative;

		&:after {
			content: '';
			width: 100%;
			height: 1px;
			position: absolute;
			background-color: $red2;
			left: 0;
			top: 50%;
			transform: translateY(-50%) skew(7deg, 7deg);
		}
	}
	&__bonus {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		color: #000000;
		margin-top: #{$inner-margin/2};
		span {
			font-weight: 800;
			font-size: 35px;
			line-height: 43px;
			text-transform: uppercase;
			color: #000000;
			margin-right: 15px;
		}
	}
	@include _mobile {
		margin-bottom: #{$inner-margin - 10px};
	}
	@include _tablet {
		&__main {
			width: 38%;
		}
		&__discount {
			width: 20%;
		}

		&__discount-percent {
			width: fit-content;
		}
		&__discount-old-price {
			width: fit-content;
		}
	}
}
.description-price.price {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	.quantity {
		width: fit-content;
	}

	@include _mobile {
		justify-content: flex-start;
		.quantity {
			order: 2;
		}
	}

	@include _tablet {
		.quantity {
			width: 38%;
		}
	}
}
