// unique buttons
@import 'btn-logout';
@import 'load-more';
@import 'btn-upload';
@import 'btn-to-top';
@import 'btn-help';

.btn {
	@include btn();
}

.filter-btn {
	@include btn($background-color: $blue2, $padding: 13px 20px);
	display: none;
	@include _mobile-tablet {
		display: flex;
		justify-content: space-between;
		max-width: 300px;
		width: 100%;
		margin-right: auto;
		svg {
			width: 20px;
			height: 20px;
		}
	}
	@include _mobile {
		margin-bottom: 20px;
	}
}

.hero-item__btn {
	@include btn($padding: 14px 130px);
	@include _mobile {
		@include btn($padding: 14px 60px);
		position: relative;
		margin-left: auto;
		margin-right: auto;
		max-width: 320px;
		width: 100%;
		z-index: 2;
	}
	@include _laptop-sm {
		@include btn($padding: 14px 100px);
	}
}
.product-card__buy-btn {
	@include btn($font-size: 12px, $line-height: 15pkx, $background-color: $blue2, $padding: 8px 16px);
	margin-right: auto;
	margin-left: auto;

	@include _mobile {
		padding: 4.5px 16px;
	}
}
.instagram__btn {
	@include btn($padding: 13px 115px);
	@include _mobile {
		position: absolute;
		width: 100%;
		bottom: 0;
		border-radius: 0;
		left: 0;
	}
}
.account-form__submit-btn {
	@include btn($padding: 14px 50px);
	margin-bottom: $inner-margin;
	// padding: 14px 50px;
	width: 100%;
}
.account-heading__btn {
	@include btn($padding: 13px 20px, $background-color: #fff, $color: #000, $border-color: $blue);
	width: 100%;
}
.account-heading__btn.active {
	@include btn($background-color: $blue, $padding: 14px 20px);
	width: 100%;
}
.account-data__btn {
	@include btn($padding: 13px 62px, $color: $blue, $background-color: #fff, $border-color: $blue);
	margin-left: auto;
	margin-right: auto;
	@include _mobile {
		max-width: 400px;
		width: 100%;
	}
}
.callback-request__btn {
	@include btn($padding: 14px 62px);

	@include _mobile {
		margin-left: auto;
		margin-right: auto;
	}
}
.loyalty__btn {
	@include btn($padding: 7px 25px, $line-height: 18px);
	text-align: center;

	@include _mobile {
		@include btn($padding: 7px 10px, $line-height: 18px, $background-color: #fff, $border-color: $blue, $color: $blue);
	}
}
.catalog-result__btn {
	@include btn($padding: 14px 100px, $background-color: #fff, $border-color: $blue, $color: $blue);
	margin-left: auto;
	margin-right: auto;
	margin-top: #{$inner-margin + 10px};
	@include _mobile-tablet {
		display: none;
	}
}
.comparison-card__buy-btn {
	@include btn($padding: 6px 25px, $background-color: $blue2);
	@include _mobile {
	}
	@include _tablet {
		margin-left: auto;
		margin-right: auto;
	}
}
.contacts-item__btn {
	@include btn($padding: 14px 62px, $color: #fff !important);
	margin-top: 25px;
	@include _mobile {
		margin-left: auto;
		margin-right: auto;
	}
}
.checkout-form__btn-submit {
	@include btn();
	max-width: 430px;
	margin-bottom: #{$inner-margin - 10px};
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	@include _mobile {
		max-width: 250px;
		margin-left: auto;
		margin-right: auto;
		padding: 14px 20px;
	}
}
.description-actions__btn {
	@include btn($padding: 14px 30px);
	width: 100%;
	margin-bottom: #{$inner-margin / 2};
}
.description-actions__btn-border {
	@include btn(
		$padding: 14px 30px,
		$background-color: #fff,
		$color: #000,
		$border-color: #000,
		$hover-border-color: $blue
	);
	width: 100%;
}
.description-offer__btn {
	@include btn($padding: 11px 35px, $background-color: #fff, $color: $blue, $border-color: $blue);
	@include _mobile {
		padding: 9px 10px;
		text-align: center;
	}
	@include _tablet {
		padding: 11px 10px;
		font-size: 14px;
		width: 100%;
	}
}
.tabs-heading__href {
	@include btn($padding: 14px 20px, $color: #000, $background-color: #fff, $border-color: $blue);
	width: 100%;
}
.tabs-heading__href.active {
	@include btn($padding: 14px 20px);
	width: 100%;
}
.modal__btn-submit {
	@include btn($padding: 14px 20px);
	width: 100%;
	margin-left: auto;
	margin-right: auto;
}
.header-login__enter-btn {
	@include btn($padding: 15px 20px);
	border-radius: 0;
	width: 50%;
}
.header-login__registration-btn {
	@include btn($padding: 15px 20px, $background-color: #fff, $color: #000, $border-color: $blue);
	border-radius: 0 0 10px 0;
	width: 50%;
}
.newsletter__btn {
	@include btn($padding: 14px 25px);
	@include _mobile-tablet {
		max-width: 350px;
	}
	@include _mobile {
		// margin-left: auto;
		margin-right: auto;
	}
	width: 100%;
}
.product-carousel__btn {
	display: none;
	@include _mobile-tablet {
		@include btn($padding: 13px 20px, $background-color: transparent, $border-color: #000, $color: #000);
		margin-left: auto;
		margin-right: auto;
		max-width: 320px;
		width: 100%;
		margin-top: 20px;
		svg {
			width: 6px;
			height: 12px;
			margin-left: 10px;
		}
	}
}
.brand__btn {
	display: none;
	@include _mobile-tablet {
		@include btn($padding: 13px 25px, $border-color: #000, $color: #000, $background-color: transparent);
		max-width: 320px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		margin-top: #{$inner-margin - 10px};
	}
}
.instagram-gallery__btn {
	display: none;
	@include _mobile-tablet {
		@include btn($padding: 13px 25px, $border-color: #000, $color: #000, $background-color: transparent);
		max-width: 320px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		margin-top: #{$inner-margin - 10px};
	}
}
.blog-section__btn {
	display: none;
	@include _mobile-tablet {
		@include btn($padding: 13px 25px, $border-color: #000, $color: #000, $background-color: transparent);
		max-width: 320px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		margin-top: #{$inner-margin - 10px};
	}
}
