.submenu-category {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding: 20px 17px 17px 27px;
	width: 300px;
	min-width: 300px;
	background-color: $grey3;
	cursor: pointer;
	border-bottom: 1px solid #fff;
	z-index: 1;
	@extend %ul-reset;
	white-space: normal;
	&:last-child {
		border-bottom: none;
		border-radius: 0 0 0 25px;
	}
	&__href {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-decoration: none;
		img {
			width: 25px;
			min-width: 25px;
			height: 32px;
			margin-right: 3px;
		}
		span {
			font-weight: bold;
			font-size: 20px;
			line-height: 24px;
			color: #000;
			transition: 0.3s all;
			max-height: 48px;
			overflow: hidden;
		}
	}
	&__toggle {
		width: 20px;
		height: 20px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-left: auto;
		min-width: 20px;
		svg {
			width: 100%;
			height: 100%;
			color: #b5b5b5;
			transition: 0.3s all;
		}
	}

	@include _mobile-tablet {
		width: 100%;
		background-color: #fff;
		margin-bottom: 15px;
		padding: 0;
		&__href {
			margin-bottom: 0;
			padding: 0;
			span {
				font-weight: 300;
				font-size: 18px;
				line-height: 22px;
				display: flex;
				align-items: center;
			}
			img {
				display: none;
			}
		}
		&__toggle {
			display: none;
		}
	}
	@include _laptop-sm {
		width: 200px;
		min-width: 200px;
	}
}

.submenu-category.active {
	background-color: #fff;
	.submenu-category__href {
		span {
			color: rgba(123, 123, 123, 0);
			background: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
			background-clip: text;
			color: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
		}
	}
	.submenu-category__toggle {
		svg {
			color: $orange;
		}
	}
}

.submenu-categories {
	@extend %ul-reset;
	overflow-y: auto;
	height: 100%;

	@include _mobile-tablet {
		margin-top: 16px;
		width: 100%;
		background-color: #fff;
	}

	@include _mobile {
		padding-left: 15px;
	}

	@include _tablet {
		padding-left: 50px;
	}
	&::-webkit-scrollbar-thumb {
		border-radius: 5px;
		width: 6px;
		background: linear-gradient(266.36deg, #f86476 0%, #fea185 34.16%);
	}
	&::-webkit-scrollbar {
		border-radius: 5px;
		background-color: $white;
		width: 6px;
	}
}
