.dropdown {
	position: relative;
	width: fit-content;
	&__toggle {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		cursor: pointer;
		svg {
			width: 10px;
			height: 5px;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: rotate(180deg);
			}
			.dropdown-menu {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

.dropdown-menu {
	position: absolute;
	@extend %ul-reset;
	background-color: #fff;
	padding: 10px;
	border-radius: 10px;
	top: 100%;
	right: 0;
	opacity: 0;
	visibility: hidden;
	transition: 0.3s all;
	&__link {
		display: block;
		text-decoration: none;
		color: #000;
		margin-bottom: 5px;
		transition: 0.3s all;
		&:hover {
			color: $orange;
		}
	}
}
