.brand {
	margin-bottom: $outter-margin;
	&__item {
		opacity: 0.5;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: 0.3s all;
		&:hover,
		&:focus {
			opacity: 1;
		}
	}
	.owl-dots {
		margin-top: $inner-margin;
	}
	.owl-nav {
		top: 40%;
	}
	@include _mobile {
		margin-bottom: $outter-margin_mobile;
		&__item {
			max-width: 230px;
			max-height: 120px;
			margin-left: auto;
			margin-right: auto;
			img {
				height: 100% !important;
				width: auto !important;
			}
		}
	}
	@include _tablet {
		.section-title {
			text-align: left;
		}
	}
}
