.comparison-card {
	padding: 12px 13px 30px 13px;
	border-radius: 10px 10px 0px 0px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-direction: column;
	background-color: $grey3;
	position: relative;
	height: 100%;
	width: 100%;
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 5px;
		width: 93px;
		height: 130px;
		img {
			transition: 0.3s all;
		}
		&:hover {
			img {
				transform: scale(1.1);
			}
		}
	}
	&__title {
		font-weight: normal;
		margin-bottom: #{$inner-margin / 2};
		text-decoration: none;
		font-size: 18px;
		line-height: 22px;
		text-transform: uppercase;
		color: #000000;
		&:hover {
			text-decoration: underline;
		}
	}
	.row {
		width: 100%;
	}
	&__price {
		font-weight: bold;
		font-size: 25px;
		line-height: 30px;
		text-transform: uppercase;
		color: #000000;
	}
	.btn {
		padding: 6px 35px;
	}
	&__delete-btn {
		position: absolute;
		padding: 6px;
		width: 20px;
		height: 20px;
		top: 10px;
		right: 10px;
		transition: 0.3s all;
		display: flex;
		align-items: center;
		justify-content: center;
		svg {
			width: 8px;
			height: 8px;
			color: #000;
			transition: 0.3s all;
		}
		&:hover {
			width: 24px;
			height: 24px;
			svg {
				width: 12px;
				height: 12px;
			}
		}
	}

	@include _mobile {
		&__price {
			margin-bottom: 20px;
		}
		&__title {
			@include adaptiv-font(18, 14);
		}
	}

	@include _tablet {
		padding: 12px 15px 20px 20px;

		&__title {
			@include adaptiv-font(18, 14);
		}

		&__price {
			margin-bottom: 20px;
		}
	}
}
