.instagram-gallery {
	margin-bottom: $outter-margin;
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 25px;
		overflow: hidden;
		img {
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			img {
				transform: scale(1.1);
			}
		}
	}
	.owl-dots {
		@include _owl-line-dots;
	}

	@include _mobile {
		margin-bottom: $outter-margin_mobile;
		&__img {
			margin-left: auto;
			margin-right: auto;
			max-width: 420px;
		}
		.owl-carousel {
			padding: 0 15px;
			left: 0;
		}

		.owl-dots {
			margin-top: 20px;
			max-width: 420px;
		}
	}

	@include _tablet {
		margin-bottom: $outter-margin_tablet;
		.owl-carousel {
			padding: 0 50px;
			max-width: 768px;
			margin-left: auto;
			margin-right: auto;
			left: 0;
			// width: calc(100% - 30px);
		}
		.owl-dots {
			padding: 0;
			margin-top: 20px;
		}
		.section-heading {
			justify-content: flex-start;
		}
	}
}
