.btn-logout {
	width: fit-content;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
	color: #000000;
	text-decoration: none;
	svg {
		width: 14px;
		height: 14px;
		margin-left: 10px;
		transition: 0.3s all;
	}
	&:hover {
		svg {
			transform: translateX(5px);
		}
	}

	@include _mobile {
	}
}
