.modal {
	display: none;
	text-align: center;
	max-width: 500px;
	padding: 20px 20px 40px 20px;
	background-color: #ffffff;
	border-radius: 10px;
	overflow: visible;
	position: relative;
	width: 100%;
	&__img {
		margin-bottom: $inner-margin;
	}
	&__decoration-img {
		position: absolute;
		z-index: 1;
		pointer-events: none;
		&_newsletter {
			top: 65px;
			right: 10px;
		}
		&_callback {
			top: 80px;
			right: 0;
		}
	}

	&__text {
		max-width: 375px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
	}
	&__title {
		font-weight: 800;
		font-size: 40px;
		line-height: 1.2;
		text-align: center;
		text-transform: uppercase;
		// max-width: 340px;
		margin-left: auto;
		margin-right: auto;
		color: #000000;
		margin-bottom: #{$inner-margin - 20px};
		&_wide {
			max-width: none;
		}
		&_red {
			color: $red;
		}
		&_green {
			color: $green2;
		}
	}
	&__subtitle {
		font-weight: normal;
		font-size: 20px;
		line-height: 1.2;
		text-align: center;
		color: #000000;
		margin-bottom: $inner-margin;
		// max-width: 370px;
		margin-left: auto;
		margin-right: auto;
	}
	.input {
		margin-bottom: 25px;
	}
	.btn {
		padding: 14px 20px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
	}
	.fancybox-close-small {
		top: -50px !important;
		right: -10px !important;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: 17px 17px;
		background-image: url(../img/cross2.svg);
		transition: 0.3s all;
		svg {
			display: none;
			opacity: 0;
		}
	}

	@include _mobile {
		padding: 10px 7px 16px 7px;
		&__decoration-img {
			display: none;
		}

		&__title {
			@include adaptiv-font(40, 30);
		}

		&__subtitle {
			@include adaptiv-font(20, 14);
		}
	}
}
