.catalog-result {
	display: flex;
	align-items: stretch;
	justify-content: flex-start;
	flex-wrap: wrap;
	.product-card {
		width: 33.33%;
		padding: 30px 0;
		border-right: 1px solid $white;
		border-bottom: 1px solid $white;
		&:nth-child(3n + 3) {
			border-right: none;
		}
	}
	&__col {
		width: 100%;
		.btn {
			padding: 14px 100px;
			margin-left: auto;
			margin-right: auto;
			margin-top: #{$inner-margin + 10px};
		}
	}
	@include _mobile {
		.product-card {
			width: 50%;
		}
		.pagination {
			margin-top: 20px;
		}
	}

	@include _tablet {
		.product-card {
			width: 50%;
		}
	}
}
