.checkbox-value {
	input {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	.checkmark {
		border: 1px solid $grey3;
		box-sizing: border-box;
		border-radius: 5px;
		padding: 9px 20px;
		cursor: pointer;
		font-weight: 300;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		transition: 0.3s all;
	}
	input:checked ~ .checkmark {
		font-weight: bold;
		color: #fff;
		background-color: $blue;
	}
}

.filter__checkbox {
	@include checkbox();
	.checkmark {
		@include checkmark();
	}
}
.filter__color {
	@include checkbox-color($border-radius: 5px);
	margin-bottom: 10px;
	margin-right: 7px;
}
.checkout-form__checkbox {
	@include checkbox();
	margin-bottom: #{$inner-margin - 10px};
	.checkmark {
		@include checkmark($border-radius: 0, $border-color: #000);
		min-width: 13px;
	}
}
.description-options__checkbox {
	@include checkbox-color($border-radius: 5px, $width: 52px, $height: 25px);
	margin-right: #{$inner-margin - 10px};
	margin-bottom: #{$inner-margin - 20px};
}
