.account-heading {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	&__avatar {
		width: 100%;
		img {
			margin-left: auto;
			margin-right: auto;
			width: 180px;
			height: 180px;
			border-radius: 50%;
		}
		.btn-upload {
			position: relative;
			top: -18px;
		}
	}
	&__name {
		width: 100%;
		margin-bottom: $inner-margin;
		position: relative;
		.name {
			font-weight: 800;
			width: fit-content;
			font-size: 40px;
			line-height: 1.4;
			color: #000000;
			margin-left: auto;
			margin-right: auto;
		}
		.btn-logout {
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
		}
	}
	&__discount {
		color: #fff;
		width: 100%;
		background: linear-gradient(266.36deg, #f86476 0%, #fea185 65.63%);
		border-radius: 10px;
		padding: 20px 25px;
		position: relative;
		z-index: 1;
		overflow: hidden;
		margin-bottom: $inner-margin;
		&::before {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background-image: url('../img/discount-pattern.svg');
			background-repeat: repeat;
			background-size: 55px 55px;
			background-position: 10px 30px;
			z-index: -1;
		}
		.subtitle-3 {
			position: relative;
			&::before {
				content: '';
				width: 1px;
				height: 100%;
				position: absolute;
				background-color: #fff;
				left: -20px;
			}
		}
		.bonus-account {
			display: flex;
			align-items: center;
			justify-content: space-between;
			background-color: #fff;
			box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.1);
			border-radius: 10px;
			padding: 3px 25px 3px 15px;
			.title-h4 {
				color: #000;
				margin-right: 5px;
				text-align: center;
				span {
					font-weight: bold;
					font-size: 20px;
					line-height: 24px;
					text-transform: uppercase;
					color: #000000;
				}
			}
			.caption {
				font-weight: bold;
				font-size: 14px;
				line-height: 14px;
				color: $grey1;
			}
		}
	}
	&__btns {
		width: 100%;
		margin-bottom: $inner-margin;
	}
	&__btns-dropdown {
		display: none;
	}
	&__toggle {
		display: none;
	}
	@include _mobile {
		&__discount {
			padding: 20px 10px;
			margin-bottom: #{$inner-margin - 10px};
			.bonus-account {
				margin-top: #{$inner-margin / 2};
				max-width: 400px;
				margin-right: auto;
			}
		}
		&__toggle {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 14px 20px 14px 15px;
			border-radius: 50px;
			background-color: $blue;
			color: #fff;
			cursor: pointer;
			span {
				font-weight: bold;
				font-size: 18px;
				line-height: 22px;
				color: #ffffff;
			}
			svg {
				width: 7px;
				height: 14px;
				transform: rotate(90deg);
				transition: 0.3s all;
			}

			&.active {
				svg {
					transform: rotate(-90deg);
				}
			}
		}

		&__btn {
			margin-bottom: 10px;
		}
		&__btns {
			position: relative;
			.row {
				display: none;
			}
		}

		&__btns-dropdown {
			position: absolute;
			width: 100%;
			top: calc(100% + 10px);
			z-index: 2;
		}
		&__name {
			.name {
				@include adaptiv-font(40, 35);
			}
			.btn-logout {
				position: static;
				margin-left: auto;
				margin-right: auto;
				transform: none;
			}
		}
	}
}
