.checkout-products {
	width: 100%;
	&__title {
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		color: #000000;
	}

	&__order-details {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		width: 100%;
		margin-top: #{$inner-margin - 10px};
	}
	&__key,
	&__value {
		width: 50%;
		font-weight: normal;
		font-size: 16px;
		line-height: 19px;
		color: #000000;
		margin-bottom: #{$inner-margin / 2};
		&_total {
			color: $blue;
		}
	}

	&__key_total {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
	}
	&__value {
		text-align: right;
		&_total {
			font-weight: bold;
			font-size: 30px;
			line-height: 37px;
			text-transform: uppercase;
		}
	}

	@include _mobile {
		margin-bottom: $inner-margin;
		&__key,
		&__value {
			margin-bottom: #{$inner-margin - 20px};
		}
		&__key {
			width: 60%;
			&_total {
				width: 30%;
			}
		}
		&__value {
			width: 40%;
			&_total {
				width: 70%;
			}
		}
	}

	@include _tablet {
		margin-bottom: #{$inner-margin - 15px};
	}

	@media (min-width: 1024px) {
		position: sticky;
		top: 140px;
	}
}
