.instagram {
	position: relative;
	z-index: 1;
	.title-h3 {
		margin-bottom: #{$inner-margin - 15px};
		text-transform: uppercase;
	}
	.title_thin {
		font-weight: 300;
		margin-bottom: $inner-margin;
	}
	&__subtitle {
		font-weight: normal;
		font-size: 20px;
		line-height: 1.2;
		color: #000000;
		padding-left: 20px;
		padding-top: 15px;
		padding-bottom: 15px;
		border-left: 1px solid #000;
		margin-bottom: $inner-margin;
	}
	&_grey {
		background-color: $grey5;
	}

	@include _mobile {
		padding: 30px 0 50px 0;
		.title_thin {
			display: none;
		}

		&__subtitle {
			@include adaptiv-font(20, 14);
			br {
				display: none;
			}
		}
	}
	@include _tablet {
		padding: 40px 0 80px 0;
		.title_thin {
			display: none;
		}

		&__subtitle {
			margin-bottom: #{$inner-margin - 10px};
			padding-top: 5px;
			padding-bottom: 5px;
			br {
				display: none;
			}
		}
		&__img {
			position: absolute;
			bottom: 0;
			height: 100%;
			right: 0;
			z-index: -1;
			img {
				height: 100%;
				width: auto;
			}
		}
	}
}
