.load-more {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	text-decoration: none;
	span {
		font-weight: normal;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		color: #000000;
		margin-right: 10px;
	}
	svg {
		width: 12px;
		height: 12px;
		color: #000;
		transition: 0.3s all;
	}
	&:hover,
	&:focus {
		svg {
			transform: translateX(5px);
		}
	}

	@include _mobile-tablet {
		display: none;
	}
}
