.description-heading {
	padding-bottom: $inner-margin;
	margin-bottom: $inner-margin;
	border-bottom: 1px solid $white;
	.title-h3 {
		margin-top: 0;
		margin-bottom: #{$inner-margin / 2};
	}
	.rating {
		margin-bottom: #{$inner-margin - 10px};
	}
	&__row {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	.status {
		margin-right: 50px;
		span {
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
		}
		svg {
			width: 12px;
			height: 8px;
			fill: transparent;
		}
		&_green {
			color: $green2;
		}
	}
	.vendore-code {
		font-weight: 300;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		color: $grey2;
	}
	@include _mobile {
		padding-bottom: 0;
		border-bottom: none;
		.rating {
			&__item {
				width: 16px;
				height: 16px;
				margin-right: 10px;
			}
		}
	}
}
