.categories-carousel {
	background-color: #fff;
	position: relative;
	margin-bottom: $outter-margin;
	display: flex;

	&::before,
	&::after {
		content: '';
		width: 210px;
		height: 100%;
		position: absolute;
		z-index: 2;
	}
	&::before {
		left: 0;
		top: 0;
		background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
	}
	&::after {
		right: 0;
		top: 0;
		background: linear-gradient(260deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
	}

	.owl-dots {
		@include _owl-line-dots(1500px, 2px, 6px); //1.width 2. height grey line 3. height active line
	}

	@include _mobile-tablet {
		margin-bottom: 0;
		&::before,
		&::after {
			content: none;
		}
		.owl-dots {
			display: none;
		}
	}
}
