.stickers {
	&__item {
		@extend %sticker-text;
		padding: 7px 20px;
		border-radius: 100px;
		&_red {
			color: #fff;
			background-color: $red;
		}
		&_yellow {
			color: #fff;
			background-color: $yellow;
		}

		&_violet {
			color: #fff;
			background-color: $violet;
		}
	}

	@include _mobile {
		&__item {
			padding: 3px 12px;
		}
	}
	@include _tablet {
		&__item {
			padding: 5px 20px;
		}
	}
}
