.newsletter {
	background-color: $grey5;
	min-height: 250px;
	position: relative;
	z-index: 1;
	padding: $outter-padding;
	&__img {
		position: absolute;
		z-index: -1;
		&_left {
			left: 0;
			bottom: 0;
			height: 100%;
			img {
				height: 100%;
			}
		}
		&_right {
			right: 30px;
			bottom: -100px;
		}
	}
	&__title {
		@extend %title4;
		text-transform: uppercase;
		color: #000;
	}
	&__subtitle {
		@extend %subtitle1;
		padding: 40px 30px;
		text-align: left;
		border-left: 1px solid #000;
	}
	.btn {
		padding: 14px 115px;
	}

	@include _mobile {
		padding: 30px 0;
		&__img {
			&_left {
				width: 100%;
				height: auto;
			}
			&_right {
				display: none;
			}
		}
		&__title {
			margin-bottom: 20px;
		}
		&__subtitle {
			margin-bottom: 220px;
			padding: 0;
			padding-left: 15px;
		}
	}
	@include _tablet {
		&__subtitle {
			padding: 0;
			padding-left: 25px;
		}
		&__img {
			&_right {
				display: none;
			}
		}
		&__title {
			text-align: left;
			margin-bottom: #{$inner-margin - 10px};
		}

		&__subtitle {
			max-width: 300px;
			margin-bottom: $inner-margin;
		}
	}
	@include _laptop-sm {
		&__img_left {
			left: -23%;
		}
	}
	@media (min-width: 1200px) and (max-width: 1379px) {
		&__img_left {
			left: -15%;
		}
	}
	@media (min-width: 1380px) and (max-width: 1500px) {
		&__img_left {
			left: -10%;
		}
	}
}
