.price-ranges {
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: relative;
	margin-bottom: 15px;
	&__input {
		display: inline-flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-direction: column;
		position: relative;
		input {
			outline: none;
			padding: 5px 10px;
			max-width: 90px;
			border: 1px solid $white;
			border-radius: 50px;
			text-align: center;
			width: fit-content;
		}
		&:first-child::after {
			content: '';
			width: 4px;
			height: 4px;
			border-radius: 50%;
			background-color: $grey1;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: -20px;
		}
	}

	&__values {
		display: flex;
		align-items: center;
		justify-content: space-between;

		span {
			font-weight: normal;
			font-size: 14px;
			line-height: 17px;
			color: #000000;
		}
	}

	@include _mobile {
		&__input:first-child::after {
			content: none;
		}
	}
}

.noUi {
	&-handle {
		top: -3px !important;
		right: -5px !important;
		border-radius: 50%;
		cursor: pointer;
		width: 11px !important;
		height: 11px !important;
		background-color: $blue2;
		border: none;
		opacity: 1;
		&::after,
		&::before {
			content: none;
		}
		&::before {
			content: '';
			width: 5.5px;
			height: 5.5px;
			background-color: #fff;
			border-radius: 50%;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}

		&-lower {
			display: none;
			opacity: 0;
		}
	}
	&-target {
		box-shadow: none;
		border: none;
		margin-bottom: 5px;
	}
	&-horizontal {
		height: 5px;
		border-radius: 4px;
		background-color: $white;
	}
	&-connect {
		background-color: $blue2;
		height: 5px;
	}
}
