.tabs {
	margin-bottom: $outter-margin;
}

.tabs-heading {
	max-width: 1110px;
	margin-left: auto;
	margin-right: auto;
	position: relative;
	bottom: -25px;
	&__href {
		span {
			font-weight: 300;
			font-size: 14px;
			line-height: 17px;
			text-align: center;
			color: #000000;
			margin-left: 10px;
			transition: 0.3s all;
		}
		&:hover span {
			color: #fff;
		}
	}
	&__href.active {
		span {
			color: #fff;
		}
	}
	&__toggle {
		display: none;
	}
	@include _mobile {
		bottom: 0;
		&__mobile {
			position: absolute;
			width: 100%;
			border-radius: 0 0 25px 25px;
			overflow: hidden;
			z-index: 2;
			display: none;
		}
		&__toggle {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 15px 15px;
			background-color: $blue;
			color: #fff;
			font-weight: bold;
			font-size: 18px;
			line-height: 22px;
			border-radius: 25px 25px 0 0;
			svg {
				width: 14px;
				height: 14px;
				color: #fff;
				transform: rotate(-90deg);
				transition: 0.3s all;
			}
		}

		&__toggle.active {
			svg {
				transform: rotate(90deg);
			}
		}
		&__href,
		&__href.active {
			border-radius: 0;
			border-top: 0;
		}

		&__href:last-child {
			border-radius: 0 0 25px 25px;
		}
	}
}

.tabs-pane {
	background: #f3f3f3;
	border-radius: 25px;
	padding: 90px 75px 90px 90px;
	.title {
		margin-top: 0;
		margin-bottom: $inner-margin;
	}
	&__item {
		opacity: 0;
		visibility: hidden;
		height: 0;
		transition: 0.3s opacity;
	}
	&__item.active {
		opacity: 1;
		visibility: visible;
		position: static;
		height: 100%;
	}
	&__item-section {
		margin-bottom: $outter-margin;
	}
	&__item-section.last {
		margin-bottom: 0;
	}
	.video {
		position: relative;
		border-radius: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		overflow: hidden;
		.btn-play {
			position: absolute;
			z-index: 2;
			width: 132px;
			height: 146px;
			transition: 0.3s all;
		}

		&:hover,
		&:focus {
			.btn-play {
				transform: scale(1.1);
			}
		}
	}
	ul {
		@extend %ul-main;
	}
	ol {
		@extend %ol-main;
	}
	.readmore-js-toggle {
		display: none;
	}

	@include _mobile {
		border-radius: 0 0 25px 25px;
		padding: 20px 10px;
		&__item-section {
			margin-bottom: $outter-margin_mobile;
		}
		.readmore-js-toggle {
			display: block;
			color: $blue;
			text-decoration: none;
			font-size: 16px;
			line-height: 1.05;
			svg {
				color: $blue;
				transform: rotate(90deg);
				width: 11px;
				height: 11px;
			}
		}
		.title {
			margin-bottom: 15px;
		}
		p {
			font-weight: 300;
			margin-top: 0;
			@include adaptiv-font(16, 14);
			line-height: 1.2;
		}
		.video {
			height: 260px;
			.btn-play {
				position: absolute;
				z-index: 2;
				width: 73px;
				height: 66px;
				transition: 0.3s all;
				svg {
					width: 100%;
					height: 100%;
				}
			}
			img {
				height: 100%;
				object-fit: cover;
			}
		}
		.readmore-js-collapsed {
			position: relative;
			width: auto;
			&::after {
				content: '';
				width: calc(100% - 10px);
				height: 100%;
				background: linear-gradient(
					180deg,
					rgba(255, 255, 255, 0) 60%,
					rgba(255, 255, 255, 0.4) 90%,
					rgba(243, 243, 243, 1) 100%
				);
				position: absolute;
				top: 0;
				left: 5px;
			}
		}
	}
	@include _tablet {
		padding: 60px 35px 30px 35px;
		&__item-section {
			margin-bottom: $outter-margin_tablet;
		}
		.video {
			.btn-play {
				width: 63px;
				height: 57px;
				svg {
					width: 100%;
					height: 100%;
				}
			}
		}
	}
}
