.reviews-card {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	background-color: #ffffff;
	box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
	border-radius: 25px;
	padding: 25px 45px;
	margin-bottom: $inner-margin;
	&:last-child {
		margin-bottom: 0;
	}
	&__product {
		padding-right: 45px;
		min-width: 220px;
		height: 100%;
	}

	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 5px;
		width: 60px;
		height: 120px;
		margin-left: auto;
		margin-right: auto;
	}
	&__title {
		font-weight: normal;
		font-size: 16px;
		line-height: 17px;
		text-align: center;
		color: $grey2;
	}
	&__description-heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: #{$inner-margin - 10px};
	}
	&__name {
		display: none;
		font-weight: 800;
		font-size: 22px;
		line-height: 1.14;
		color: #000000;
	}
	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}
	.rating {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		svg {
			width: 15px;
			height: 15px;
			margin-right: 10px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	p {
		font-style: italic;
		font-weight: 300;
		font-size: 16px;
		line-height: 19px;
		color: #000000;
		margin-bottom: #{$inner-margin - 10px};
	}
	.status {
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		svg {
			width: 13px;
			height: 10px;
			fill: transparent;
			margin-right: 10px;
		}
		&_green {
			color: $green2;
			svg {
				color: $green2;
			}
		}
		&_red {
			color: $red;
			svg {
				color: $red;
			}
		}
		&_blue {
			color: $blue;
			svg {
				color: $blue;
			}
		}
	}
	.date {
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		color: $grey1;
	}
	@include _mobile {
		flex-direction: column;
		padding: 20px;
		padding-bottom: 10px;
		&__name {
			display: block;
			margin-right: 10px;
		}
		.date,
		.status {
			margin-bottom: 15px;
		}
		&__img {
			margin-left: 0;
			margin-right: 15px;
		}
		.status {
			margin-right: 5px;
		}

		&__product {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			flex-direction: row;
			padding-right: 0;
		}
		&__bottom {
			flex-wrap: wrap;
		}

		&__description-heading {
			flex-wrap: wrap;
			margin-bottom: 0;
		}

		&__name,
		.rating {
			margin-bottom: 13px;
		}
	}

	@include _tablet {
		&__product {
			min-width: 150px;
		}
		&__name {
			display: block;
		}
	}
}
