.delivery-payment {
	margin-bottom: $outter-margin;
	.container {
		position: relative;
	}
	.title_h4 {
		margin-bottom: 15px;
	}
	img {
		margin-bottom: 35px;
		position: relative;
		z-index: 2;
	}
	.line {
		width: 1px;
		height: calc(100% + #{$section-margin} - 135px);
		position: absolute;
		background-color: $white2;
		left: 50%;
		transform: translateX(-50%);
		bottom: -$section-margin;
	}
	@include _mobile-tablet {
		.line {
			display: none;
		}
	}
	@include _mobile {
		margin-bottom: #{$outter-margin_mobile - 20px};
	}
	@include _tablet {
		margin-bottom: $outter-margin_tablet;
	}
}
