.hero-item {
	&__title {
		margin-top: 0;
		margin-bottom: 10px;
		text-transform: uppercase;
		@extend %title1;
		width: 100%;
	}
	&__subtitle {
		font-weight: 300;
		font-size: 40px;
		line-height: 1.2;
		text-transform: uppercase;
		color: #000000;
		margin-bottom: $inner-margin;
		width: 100%;
	}
	&__text {
		font-weight: 300;
		font-size: 18px;
		line-height: 1.12;
		color: #000000;
		margin-bottom: $inner-margin;
		width: 100%;
	}
	&__img {
		margin-right: 110px;
		position: relative;
		top: -90px;
		&_bottom {
			top: 0;
		}
	}

	&__img-mobile {
		display: none;
	}

	@include _mobile {
		padding: $outter-padding_mobile;
		br {
			display: none;
		}
		&__img {
			display: none;
		}

		&__wrapper {
			display: flex;
			align-items: center;
			flex-direction: column;
			width: 100%;
		}

		&__img-mobile {
			display: flex;
			align-items: center;
			justify-content: center;
			align-content: center;
			max-width: 370px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: -50px;
		}
		&__title {
			margin-bottom: #{$inner-margin - 20px};
		}
		&__subtitle {
			@include adaptiv-font(40, 25);
			margin-bottom: #{$inner-margin - 20px};
		}

		&__text {
			@include adaptiv-font(18, 14);
			margin-bottom: #{$inner-margin - 20px};
		}
	}

	@include _tablet {
		position: relative;
		padding: 50px 0 105px 0;
		&__img {
			position: absolute;
			z-index: -1;
			height: 430px;
			margin-right: 0;
			right: 0;
			bottom: 0;
			top: auto;
			img {
				height: 100%;
			}
		}
		&__subtitle {
			margin-bottom: #{$inner-margin - 10px};
		}
	}

	@include _laptop-sm {
		&__img {
			margin-right: 0;
		}
	}
}
