.category-card {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	text-decoration: none;
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			transition: 0.3s all;
		}
	}
	&__title {
		font-weight: normal;
		font-size: 20px;
		line-height: 1.2;
		color: #000000;
	}

	@include _mobile {
		&__title {
			@include adaptiv-font(20, 12);
			max-height: 28px;
			overflow: hidden;
			text-align: center;
		}
	}
}

@import '../category-cards/category-card-carousel';
@import '../category-cards/category-card-center';
