.submenu {
	position: absolute;
	padding: 0;
	margin: 0;
	list-style: none;
	border-radius: 25px;
	z-index: 30;
	top: calc(100% + 50px);
	height: 550px;
	opacity: 0;
	overflow: hidden;
	border-radius: 25px;
	visibility: hidden;
	width: 100%;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	pointer-events: none;
	// white-space: pre-wrap;
	&:hover {
		.header-categories__item::after {
			pointer-events: none;
		}
	}

	@include _mobile-tablet {
		position: fixed;
		opacity: 1;
		visibility: visible;
		z-index: 333;
		width: 100%;
		top: 87px;
		left: 0;
		pointer-events: inherit;
		transform: translateX(-200%);
		transition: 0.3s all;
		&.active {
			transform: translateX(0);
		}
	}

	@include _laptop-sm {
		width: 950px;
		left: -200px;
	}

	@include _laptop {
		width: 1100px;
		left: -190px;
	}

	@media (min-width: 1600px) {
		width: 1480px;
		left: -280px;
	}
}

.submenu.active {
	opacity: 1;
	visibility: visible;
	pointer-events: inherit;
}

@import './subemenu-categories';
@import './submenu-items';
