.btn-to-top {
	display: none;
	@include _mobile-tablet {
		position: fixed;
		background-color: $blue;
		color: #fff;
		bottom: 0;
		right: 3%;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 30;
		border-radius: 100px 100px 0 0;
		padding-bottom: 100px;
		width: 50px;
		span {
			display: block;
			position: absolute;
			top: 30px;
			transform: rotate(90deg);
		}
	}
}
