.article-heading {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: $inner-margin;
	&__info {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		font-weight: 300;
		font-size: 14px;
		line-height: 17px;
		svg {
			width: 12px;
			height: 12px;
		}
		.date {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			margin-right: 30px;
			svg {
				margin-right: 10px;
			}
		}

		.views {
			margin-right: 30px;
			svg {
				margin-right: 10px;
			}
		}
		.share {
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
			color: #000000;
			text-decoration: none;
			svg {
				margin-left: 10px;
				transition: 0.3s all;
			}
			&:hover svg {
				transform: translateX(5px);
			}
		}
	}
	&__profile {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		.avatar {
			margin-right: 20px;
		}
		.name {
			font-weight: bold;
			font-size: 20px;
			line-height: 24px;
			color: #000000;
			margin-right: 3px;
		}
		.caption {
			font-style: normal;
			font-weight: 300;
			font-size: 14px;
			line-height: 17px;
			color: #000000;
		}
	}

	@include _mobile {
		flex-wrap: wrap;
		margin-bottom: 0;
		&__info {
			margin-bottom: 15px;
			.date,
			.views {
				margin-right: 15px;
				svg {
					margin-right: 5px;
				}
			}
		}
		&__profile {
			margin-right: 10px;
			margin-bottom: 15px;
		}
	}
}
