.btn-help {
	display: none;
	@include _mobile-tablet {
		padding: 14px;
		border-radius: 50%;
		background: linear-gradient(266.36deg, #f86476 0%, #fea185 34.16%);
		display: flex;
		align-items: center;
		justify-content: center;
		position: fixed;
		right: 3%;
		bottom: 150px;
		z-index: 30;
		svg {
			color: #fff;
			width: 23px;
			height: 23px;
		}
		&__caption {
			position: absolute;
			bottom: -20px;
			font-weight: bold;
			font-size: 14px;
			line-height: 17px;
			text-align: center;
			color: #000000;
		}
	}
}
