.footer {
	padding: $section-padding;
	background-color: #fff;
	&_grey {
		background-color: $white;
	}
	&__copyright {
		font-weight: 300;
		font-size: 16px;
		line-height: 19px;
		color: rgba(75, 75, 75, 0.5);
	}
	&__title {
		font-weight: bold;
		font-size: 20px;
		line-height: 1.2;
		color: #000000;
		margin-bottom: 10px;
	}
	.logo {
		margin-bottom: 40px;
	}
	@include _mobile-tablet {
		background-color: $white;
	}
	@include _mobile {
		padding: $outter-padding_mobile;

		&__contacts {
			padding-top: 20px;
			border-top: 1px solid #dddddd;
		}
		&__copyright {
			max-width: 200px;
		}
		.logo {
			margin-bottom: 20px;
		}
		&__title {
			@include adaptiv-font(20, 18);
		}
	}
	@include _tablet {
		padding: $outter-padding_tablet;
		.logo {
			margin-bottom: 30px;
		}
		&__copyright {
			max-width: 300px;
		}
	}

	@include _laptop-sm {
		.col-md-2,
		.col-md-3 {
			padding-left: 15px;
			padding-right: 15px;
		}
		.row {
			margin-right: -15px;
			margin-left: -15px;
		}
	}
}

@import 'footer-menu';
@import 'footer-contacts';
@import 'footer-copyright';
