.filter {
	&__title {
		font-weight: bold;
		font-size: 20px;
		line-height: 23px;
		color: #000000;
	}
	&__price {
		margin-bottom: #{$inner-margin - 15px};
		padding-bottom: #{$inner-margin - 15px};
		border-bottom: 1px solid $white;
		.filter__title {
			margin-bottom: #{$inner-margin - 15px};
		}
	}
	&__heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: #{$inner-margin - 15px};
	}
	&__toggle {
		padding: 0;
		width: 15px;
		height: 15px;
		svg {
			width: 100%;
			height: 100%;
			transition: 0.3s all;
			transform: rotate(-90deg);
		}
		&.active {
			svg {
				transform: rotate(90deg);
			}
		}
	}

	&__colors {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		margin-bottom: #{$inner-margin / 2};
		.checkbox-color {
		}
	}
	&__options {
		padding-bottom: #{$inner-margin / 2};
		margin-bottom: #{$inner-margin / 2};
		border-bottom: 1px solid $white;
		&:last-child {
			border-bottom: none;
		}

		.checkbox_grey {
			margin-bottom: #{$inner-margin / 2};
			&:last-child {
				margin-bottom: 0;
			}
		}
		.checkbox__text {
			font-weight: normal;
			font-size: 16px;
			line-height: 17px;
			text-align: center;
			color: $grey1;
		}
	}
	&__mobile-heading {
		display: none;
	}
	@include _mobile-tablet {
		position: fixed;
		opacity: 0;
		visibility: hidden;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		transform: translateY(-200%);
		background-color: #fff;
		z-index: 999;
		transition: 0.3s all;
		padding: 30px 15px;
		overflow-x: auto;
		&.active {
			transform: translateY(0);
			opacity: 1;
			visibility: visible;
		}
		&__mobile-heading {
			padding: 10px 0;
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%;
			margin-bottom: 20px;
			span {
				font-weight: bold;
				font-size: 20px;
				line-height: 1.14;
				color: #000;
			}
		}
		&__close-btn {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 4px;
			width: 22px;
			height: 22px;
			svg {
				width: 14px;
				height: 14px;
			}
		}
	}
}
