.owl-nav {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: calc(100% + 200px);
	left: -100px;
	height: 0;
	top: 50%;
	button {
		opacity: 0.5;
		transition: 0.3s all;
		svg {
			color: #b5b5b5;
		}
		&:hover,
		&:focus {
			opacity: 1;
			svg {
				color: #000;
			}
		}
	}
	@include _mobile-tablet {
		display: none;
	}
	@include _laptop-sm {
		display: none;
	}
	@include _laptop {
		width: 100%;
		left: 0;
	}
	@include _large {
		width: calc(100% + 100px);
		left: -50px;
	}
}
.owl-dots {
	display: flex;
	align-items: center;
	justify-content: center;
	.owl-dot {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 7px;
		background-color: #b5b5b5 !important;
		transition: 0.3s all;
		&:last-child {
			margin-right: 0;
		}
	}
	.active {
		background-color: #000 !important;
	}
}
