.instagram-advantages {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 20px;
	&__icon {
		width: 60px;
		height: 60px;
		background-color: #ffffff;
		box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.1);
		border-radius: 15px;
		margin-right: 25px;
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			width: 36px;
			height: 36px;
		}
	}
	&__label {
		font-weight: 600;
		font-size: 20px;
		line-height: 1.2;
		color: $grey1;
		max-width: 210px;
	}

	@include _mobile {
		margin-bottom: 15px;
		&__icon {
			width: 44px;
			height: 44px;
			margin-right: 15px;
			img {
				width: 24px;
				height: 24px;
			}
		}
		&__label {
			@include adaptiv-font(20, 16);
		}
	}
}
