.footer-menu {
	@extend %ul-reset;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	flex-wrap: wrap;
	width: 100%;
	li {
		width: 50%;
	}
	&__link {
		font-weight: 300;
		font-size: 16px;
		line-height: 1.2;
		color: rgba(0, 0, 0, 1);
		display: block;
		padding-bottom: 10px;
		text-decoration: none;
		&:hover,
		&:focus {
			color: rgba(0, 0, 0, 0);
			letter-spacing: 0;
			background: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
			background-clip: text;
			color: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
		}
	}

	@include _mobile-tablet {
		li {
			width: 100%;
		}
	}
	@include _mobile {
		margin-bottom: #{$inner-margin - 10px};
		&__link {
			font-size: 14px;
			@include adaptiv-font(16, 14);
		}
	}

	@include _laptop-sm {
		li {
			width: 100%;
		}
	}
}
