.contacts-item {
	font-size: 16px;
	line-height: 19px;
	.title-h4 {
		margin-bottom: 10px;
	}
	.title-h5 {
		margin-bottom: 15px;
	}
	span {
		display: block;
		font-style: normal;
		font-weight: 300;
		color: #000000;
	}
	.span-last {
		margin-bottom: 10px;
	}
	a {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		font-weight: 600;
		color: #000;
		text-decoration: none;
		margin-bottom: 5px;
		svg {
			width: 14px;
			height: 14px;
			color: $blue;
			margin-right: 15px;
		}
	}
	.href-last {
		margin-bottom: 12px;
	}

	@include _mobile {
		margin-bottom: $inner-margin;
		padding-bottom: $inner-margin;
		border-bottom: 1px solid $white;
	}

	@include _tablet {
		margin-bottom: #{$inner-margin + 20px};

		.title-h4,
		.title-h5 {
			margin-bottom: #{$inner-margin - 20px};
		}
	}
}

.contacts-item.last {
	@include _mobile {
		padding-bottom: 0;
		border-bottom: none;
	}
}
