.header {
	position: sticky;
	top: 0;
	z-index: 10;
	background-color: #fff;
	padding-bottom: 10px;
	border-bottom: 1px solid $white;
	&__notice {
		padding: 5px 0;
		background-color: $white;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 6;
		position: relative;
		svg {
			width: 20px;
			height: 20px;
		}
	}
	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-top: 20px;
		padding-bottom: 20px;
		position: relative;
		z-index: 6;
	}
	&__backdrop {
		background: rgba(0, 0, 0, 0.7);
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 9;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		transition: 0.3s all;
	}
	&__backdrop.active {
		opacity: 1;
		visibility: visible;
		pointer-events: inherit;
	}
	.logo {
		min-width: 188px;
		height: 43px;
		margin-right: 20px;
	}

	@include _mobile {
		.logo {
			width: 132px;
			min-width: 132px;
			height: 31px;
		}
	}

	@include _mobile-tablet {
		padding-bottom: 0;
		&__notice {
			display: none;
		}
	}
}

@import 'header-categories';
@import 'header-actions';
@import 'header-info';
@import 'header-hamb';
@import 'header-cart';
@import 'header-login';
@import 'dropdowns';
@import 'submenu';
@import 'header-mobile';
