.comparison {
	margin-bottom: $section-margin;
}
.comparison-table {
	display: flex;
	align-items: flex-end;
	flex-direction: column;
	border-collapse: collapse;
	width: 100%;
	position: relative;
	z-index: 1;
	border-spacing: 0;
	overflow: auto;
	border-radius: 0 0 0 25px;
	tbody {
		width: 100%;
	}
	&__heading {
		display: flex;
		align-items: stretch;
		width: fit-content;
		justify-content: flex-end;
		margin-left: 280px;
	}
	tr:last-child {
		border-radius: 0 0 0 25px;
	}
	td {
		width: 280px;
		min-width: 280px;
		text-align: center;
		padding: 15px 10px;
		border-bottom: 1px solid $grey3;
		border-right: 1px solid $grey3;
		font-size: 20px;
		line-height: 1.2;
		color: #000000;
		&:first-child {
			border-right: 0;
		}
		&.last {
			border-radius: 0 0 0 25px;
		}
	}
	th {
		display: inline-flex;
		max-width: 280px;
		min-width: 280px;
		width: 100%;
	}
	&__properties {
		position: relative;
		&:after {
			content: '';
			position: absolute;
			width: 279px;
			height: 100%;
			background-color: #fff;
			box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05);
			border-radius: 25px 0px 0px 25px;
			bottom: 0;
			left: 1px;
			z-index: -1;
		}
	}

	@include _mobile-tablet {
	}

	@include _mobile {
		&__heading {
			margin-left: 160px;
		}
		td {
			width: 160px;
			min-width: 160px;
			@include adaptiv-font(20, 16);
		}
		th {
			max-width: 160px;
			min-width: 160px;
		}

		&__properties {
			&::after {
				width: 159px;
			}
		}
	}

	@include _tablet {
		&__heading {
			margin-left: 230px;
		}
		th {
			max-width: 230px;
			min-width: 230px;
		}
		td {
			width: 230px;
			min-width: 230px;
			font-size: 16px;
			&:first-child {
				padding-left: 30px;
			}
		}
		&__properties {
			&:after {
				width: 229px;
			}
		}
	}
}
