.container {
	max-width: 1500px;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;
	margin-right: auto;
	margin-left: auto;
	&_sm {
		max-width: 1000px;
	}
	&_md {
		max-width: 1230px;
	}
	&_xs {
		max-width: 750px;
	}
	@include _mobile {
		padding-left: 15px;
		padding-right: 15px;
	}

	@include _tablet {
		max-width: 768px;
		padding-left: 50px;
		padding-right: 50px;
	}
}
