.account-reviews {
	width: 100%;
	display: block;
	height: 400px;
	position: relative;
	margin-bottom: $outter-margin;
	&::after {
		content: '';
		position: absolute;
		background: linear-gradient(180deg, #ffffff 10.2%, rgba(255, 255, 255, 0) 100%);
		transform: rotate(-180deg);
		height: 100px;
		bottom: 0;
		width: 100%;
	}
	.reviews-card {
		margin-bottom: $inner-margin;
	}
}
