.header-login {
	position: absolute;
	background-color: #fff;
	z-index: 2;
	right: 0;
	width: 370px;
	border-radius: 20px;
	overflow: hidden;
	transform: translateY(-200%);
	top: calc(100% + 50px);
	transition: 0.3s all;
	&__heading {
		padding: 15px 30px;
		border-bottom: 1px solid $white;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__title {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		color: #000000;
	}
	&__subtitle {
		padding: 30px;
		font-weight: normal;
		font-size: 18px;
		line-height: 22px;
		text-align: center;
		color: #000000;
	}
	.btn-close {
		width: 14px;
		height: 14px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0;
		svg {
			width: 100%;
			height: 100%;
			color: #000;
		}
	}
	&__btns {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
	.btn {
		width: 50%;
		padding: 15px 20px;
		border-radius: 0;
	}
	.btn-blue-border {
		padding: 14px 20px;
		border-radius: 0 0 20px 0;
	}

	@include _mobile {
		display: none;
	}
}

.header-login.active {
	transform: translateY(0);
}
