.header-cart {
	right: 0;
	position: absolute;
	top: calc(100% + 50px);
	background-color: #fff;
	z-index: 6;
	border-radius: 10px;
	width: 365px;
	transform: translateY(-200%);
	transition: 0.3s all;
	&__heading {
		padding: 15px 20px 15px 30px;
		border-bottom: 1px solid $white;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__title {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		color: #000000;
	}
	&__total {
		padding: 15px 30px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		span {
			font-weight: normal;
			font-size: 20px;
			line-height: 24px;
			color: #000000;
		}
		b {
			font-weight: bold;
			font-size: 24px;
			line-height: 29px;
			text-transform: uppercase;
			color: #000000;
		}
	}
	.btn-close {
		svg {
			width: 14px;
			height: 14px;
			color: #000000;
		}
	}
	.btn {
		width: 100%;
		padding: 14px 20px;
		border-radius: 0 0 10px 10px;
	}
}
.header-cart.active {
	transform: translateY(0);
}
