.footer-contact {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 10px;
	text-decoration: none;
	&__icon {
		width: 18px;
		height: 18px;
		margin-right: 10px;
	}
	&__label {
		font-weight: normal;
		font-size: 16px;
		line-height: 19px;
		color: #000000;
		transition: 0.3s all;
		&:hover,
		&:focus {
			color: $orange;
		}
	}
}
