.product-card {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	.row {
		max-width: 260px;
		width: 100%;
	}
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 285px;
		margin-bottom: 20px;
		img {
			height: 100%;
			width: auto !important;
			transition: 0.3s all;
		}
	}
	&__caption {
		font-weight: normal;
		font-size: 16px;
		line-height: 1.1;
		text-align: center;
		color: $grey1;
		margin-bottom: 10px;
	}
	&__title {
		font-weight: bold;
		font-size: 16px;
		line-height: 1.05;
		text-align: center;
		color: #000000;
		text-decoration: none;
		margin-bottom: 15px;
	}
	&__price {
		.current {
			@extend %current-price;
		}
		.old {
			@extend %old-price;
		}
	}
	.rating {
		margin-bottom: 15px;
	}
	.stickers {
		position: absolute;
		left: 30px;
	}

	@include _mobile-tablet {
		&__price {
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
		}
		&__img {
			height: auto;
		}
	}

	@include _mobile {
		&__price {
			flex-direction: row;
			margin-bottom: 10px;
			.old {
				margin-right: 10px;
			}
		}
		&__img {
			max-width: 160px;
			margin-bottom: 0;
		}
		.rating {
			margin-bottom: 8px;
		}
		.stickers {
			left: 0;
			&__item {
				margin-bottom: 5px;
			}
		}
		&__title {
			@include adaptiv-font(16, 18);
			margin-bottom: 10px;
		}
		&__caption {
			@include adaptiv-font(16, 14);
		}
		.row {
			margin-left: 0;
			margin-right: 0;
		}
		.col-xs-12 {
			padding: 0;
		}
	}
	@include _tablet {
		&__price {
			flex-direction: column;
		}
	}
	@include _laptop-sm {
		&__price {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			text-align: center;
			min-height: 52px;
		}
	}

	@media (min-width: 1024px) {
		&:hover,
		&:focus {
			.product-card__img {
				img {
					transform: scale(1.1);
				}
			}
			.product-card__title {
				text-decoration: underline;
			}
		}
	}
}

.product-card-actions {
	position: absolute;
	right: 30px;
	&__btn {
		svg {
			width: 24px;
			height: 24px;
			fill: $grey1;
			transition: 0.3s all;
		}

		&:hover,
		&:focus {
			svg {
				fill: url(#orange-gradient);
			}
		}
	}

	@include _mobile {
		right: 0;
	}
}
