.header-hamb {
	&__btn {
		width: 22px;
		height: 22px;
		padding: 0;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
		overflow: hidden;
		span {
			height: 5px;
			width: 100%;
			display: block;
			background: $grey2;
			border-radius: 10px;
			pointer-events: none;
			position: relative;
			transition: 0.3s all;
		}
		&:hover,
		&:focus {
			span {
				background: linear-gradient(266.36deg, #f86476 0%, #fea185 65.63%);
			}
		}
	}

	&__btn.active {
		span:nth-child(1) {
			transform: rotate(45deg);
			top: 10px;
		}
		span:nth-child(2) {
			opacity: 0;
			visibility: hidden;
		}
		span:nth-child(3) {
			transform: rotate(-45deg);
			top: -5px;
		}
	}

	&__menu {
		@extend %ul-reset;
		position: absolute;
		padding: 10px 20px;
		width: 300px;
		background-color: #fff;
		border-radius: 10px;
		z-index: 5;
		top: calc(100% + 20px);
		transform: translateY(-200%);
		transition: 0.3s all;
		right: 0;
		a {
			font-weight: bold;
			font-size: 18px;
			line-height: 33px;
			color: $grey2;
			text-decoration: none;
			&:hover,
			&:focus {
				color: rgba(123, 123, 123, 0);
				background: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
				background-clip: text;
				color: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
			}
		}
	}

	&__menu.active {
		transform: translateY(0);
	}

	@include _mobile-tablet {
		&__btn.active {
			span:nth-child(1) {
				top: 9px;
			}
		}

		&__menu {
			display: none;
		}
	}
}
