.checkout-card {
	display: flex;
	position: relative;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	padding: 15px 10px;
	border-bottom: 1px solid $white;
	&__img {
		width: 65px;
		min-width: 65px;
		height: 85px;
		img {
			transition: 0.3s all;
		}
		&:hover {
			img {
				transform: scale(1.1);
			}
		}
	}
	&__title {
		font-weight: normal;
		font-size: 18px;
		line-height: 1.2;
		text-transform: uppercase;
		color: #000000;
		text-decoration: none;
		width: 30%;
		transition: 0.3s all;
		&:hover {
			color: $orange;
		}
	}
	&__price {
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		text-transform: uppercase;
		color: #000000;
	}
	&__col {
		display: none;
	}
	.btn-delete {
		position: absolute;
		top: 0;
		right: 0;
		padding: 0;
		width: 20px;
		height: 20px;
		display: flex;
		align-items: center;
		justify-content: center;
		svg {
			width: 8px;
			height: 8px;
			transition: 0.3s all;
		}
		&:hover {
			svg {
				transform: scale(1.2);
			}
		}
	}

	@include _mobile-tablet {
		&__col {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			flex-direction: column;
		}
	}
	@include _mobile {
		&__col {
			width: 50%;
		}
		&__title {
			width: 100%;
			margin-bottom: 10px;
			@include adaptiv-font(18, 14);
		}
		&__price {
			min-width: 65px;
			text-align: right;
		}
	}
	@include _tablet {
		justify-content: flex-start;
		&__img {
			margin-right: 30px;
		}
		&__title {
			margin-right: 30px;
		}
		&__price {
			margin-left: auto;
		}
	}
}
