.catalog-heading {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: #{$inner-margin - 15px};
	&__result {
		font-weight: 300;
		font-size: 18px;
		line-height: 22px;
		color: #000000;
		b {
			font-weight: bold;
		}
	}

	@include _mobile-tablet {
		&__result {
			display: none;
		}
	}

	@include _mobile {
		flex-direction: column;
	}
}
