.category-card_carousel {
	align-items: center;
	justify-content: flex-start;
	flex-direction: column;
	background-color: $grey3;
	padding: 20px 0;
	height: 257px;
	position: relative;
	.category-card__img {
		margin-bottom: 15px;
	}
	.category-card__title {
		text-align: center;
		// max-height: 48px;
		// overflow: hidden;
		padding: 0 10px;
		flex-grow: 1;
	}

	@include _mobile-tablet {
		background-color: #fff;
		height: 100%;
		.category-card__img {
			width: 60px;
			height: 80px;
			margin-bottom: 10px;
		}
		.category-card__title {
			max-height: none;
			// overflow: hidden;
		}
		&::after {
			content: '';
			width: 1px;
			height: 80%;
			background-color: $white;
			position: absolute;
			right: 0;
		}
	}
	@include _mobile {
		.category-card__title {
			@include adaptiv-font(20, 12);
		}
	}
	@include _tablet {
		padding: 7px;
		.category-card__title {
			line-height: 1.05;
			font-size: 12px;
			padding: 0;
		}
	}
}
