.text {
	.title-h5,
	.title-h7 {
		text-align: left;
		margin-bottom: #{$inner-margin / 2};
	}
	p {
		text-align: left;
		margin-bottom: $inner-margin;
		font-weight: 300;
		font-size: 16px;
		line-height: 1.2;
		b {
			font-weight: bold;
		}
	}
	ol {
		@extend %ol-main;
		text-align: left;
	}
	ul {
		@extend %ul-main;
		text-align: left;
	}
	.blockquote {
		font-style: italic;
		text-align: center;
		font-weight: 600;
		font-size: 18px;
		line-height: 1.2;
		color: $grey2;
		margin-bottom: $inner-margin;
	}
	.section {
		margin-bottom: $outter-margin;
		p:last-child {
			margin-bottom: 0;
		}
	}

	@include _mobile {
		p {
			@include adaptiv-font(16, 14);
			margin-top: #{$inner-margin - 20px};
			margin-bottom: #{$inner-margin - 10px};
		}
		.title-h5,
		.title-h7 {
			text-align: left;
			margin-bottom: #{$inner-margin - 20px};
		}
		.section {
			margin-bottom: $outter-margin_mobile;
			p:last-child {
				margin-bottom: $inner-margin;
			}
		}
		.blockquote {
			@include adaptiv-font(18, 14);
		}
		ul:last-child {
			margin-bottom: 0;
		}
	}

	@include _tablet {
		.section {
			margin-bottom: $outter-margin_tablet;
		}
		br {
			display: none;
		}
		p {
			font-weight: normal;
			margin-bottom: #{$inner-margin + 10px};
			margin-top: 18px;
		}
	}
}
