.table {
	margin-bottom: $inner-margin;
	margin-top: #{$inner-margin - 10px};
	font-size: 14px;
	line-height: 1.2;
	letter-spacing: 0.007px;
	background-color: $grey7;
	border-radius: 10px;
	width: 100%;
	display: flex !important;
	align-items: stretch;
	justify-content: center;
	flex-wrap: wrap;
	overflow: hidden;
	th,
	td,
	div {
		border-right: 1px solid $border;
		border-bottom: 1px solid $border;
		padding: 9px 20px;
		color: #000;
		a {
			text-decoration: none;
			color: #000;
		}
		&:last-child {
			border-right: 0;
		}
	}
	div:last-child,
	tr:last-child {
		td {
			border-bottom: 0;
		}
	}
	.blue {
		color: $blue;
		a {
			color: $blue;
		}
	}
	.center {
		text-align: center;
	}
	.left-padding {
		padding-left: 50px;
	}
	div {
		width: 33.33%;
	}

	@include _mobile {
		font-size: 13px;
		div,
		th,
		td {
			width: 50%;
			text-align: center;
			padding: 9px 10px;
		}
		.left-padding {
			padding-left: 10px;
		}
	}
}
