.pagination {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 40px;
	position: relative;
	width: fit-content;
	margin-left: auto;
	margin-right: auto;
	&::after {
		content: '';
		width: calc(100% - 44px);
		position: absolute;
		bottom: -5px;
		height: 1px;
		background-color: $grey1;
		left: 24px;
	}
	.active {
		span {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			color: $blue;
		}
		&:after {
			content: '';
			width: 100%;
			position: absolute;
			bottom: -6px;
			height: 3px;
			background-color: $blue;
			z-index: 1;
		}
	}
	&__item {
		width: 22px;
		height: 22px;
		margin-right: 25px;
		position: relative;

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			text-decoration: none;
			color: $grey1;
		}

		&_last {
			margin-right: 0;
			&:after {
				width: 100%;
			}
		}
	}
	&__next,
	&__prev {
		width: 12px;
		height: 12px;
		display: flex;
		align-items: center;
		justify-content: center;
		a {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__next {
		margin-right: 12px;
		svg {
			transform: rotate(90deg);
		}
	}
	&__prev {
		margin-left: 12px;
		svg {
			transform: rotate(-90deg);
		}
	}

	@include _mobile {
		margin-top: 0;
		&__item {
			margin-right: 10px;
		}
	}
}
