.header-actions {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	&__item {
		width: 22px;
		height: 22px;
		margin-right: 25px;
		cursor: pointer;
		svg {
			width: 100%;
			height: 100%;
			fill: $grey2;
			transition: 0.3s all;
			pointer-events: none;
		}
		&_sticker {
			position: relative;
		}
		&:hover,
		&:focus {
			svg {
				fill: url(#orange-gradient);
			}
		}
	}
	&__sticker {
		display: flex;
		align-content: center;
		justify-content: center;
		position: absolute;
		top: -5px;
		right: -8px;
		width: 14px;
		height: 14px;
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		color: #ffffff;
		border-radius: 50%;
		position: absolute;
		background: linear-gradient(266.36deg, #f86476 0%, #fea185 65.63%);
		pointer-events: none;
	}

	@include _mobile-tablet {
		order: 2;
	}
	@include _laptop-sm {
		margin-right: 20px;
	}
	@media (min-width: 1200px) and(max-width: 1900px) {
		margin-right: 20px;
	}
}
