.header-info {
	width: fit-content;
	position: absolute;
	top: 10px;
	left: calc(100% + 40px);
	&__item {
		margin-bottom: 5px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	&__item:last-child {
		margin-bottom: 0;
	}

	.caption {
		margin-right: 20px;
		font-size: 14px;
		line-height: 17px;
		color: $black1;
	}
	.phone {
		font-weight: bold;
		font-size: 16px;
		line-height: 19px;
		color: $grey2;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			color: $orange;
		}
	}
	.lang {
		font-size: 14px;
		line-height: 17px;
		color: $grey1;
		text-decoration: none;
		transition: 0.3s all;
		&:hover {
			color: $orange;
		}
	}
	.lang:last-child {
		padding-left: 5px;
	}
	.lang:first-child {
		padding-right: 5px;
		border-right: 1px solid $grey1;
	}
	.city {
		font-weight: bold;
		font-size: 14px;
		line-height: 17px;
		color: #000000;
		text-decoration: none;
		svg {
			margin-left: 5px;
		}
	}
	.active {
		font-weight: bold;
		color: #000;
	}

	@include _mobile-tablet {
		position: static;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		align-content: center;
		order: 1;
		margin-right: 10px;
		&__wrapper {
			margin-right: 30px;
		}
		.lang:first-child {
			padding-right: 5px;
			border-right: none;
		}
		a.lang {
			display: none;
		}
	}

	@include _mobile {
		display: none;
	}

	@include _laptop-sm {
		position: static;
		min-width: 150px;
	}

	@media (min-width: 1200px) and (max-width: 1900px) {
		position: static;
		min-width: 150px;
	}
}
