// add fonts
@mixin font-face($font-family, $file-path, $weight: normal, $style: normal) {
	@font-face {
		font-family: $font-family;
		font-weight: $weight;
		font-style: $style;
		src: url('#{$file-path}.eot');
		src: url('#{$file-path}.eot?#iefix') format('embedded-opentype'), url('#{$file-path}.woff') format('woff'),
			url('#{$file-path}.woff2') format('woff2'), url('#{$file-path}.ttf') format('truetype');
	}
}
// Owl navigation like lines
@mixin _owl-line-dots($width: 1500px, $line-height: 2px, $active-dot-height: 6px, $color: #b5b5b5) {
	max-width: $width;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;
	margin-right: auto;
	margin-left: auto;
	min-height: 6px;
	margin-top: 5px;
	display: flex;
	align-items: center;
	.owl-dot {
		width: 100%;
		border-radius: 0;
		margin-right: 0;
		height: $line-height;
		background-color: $color;
	}
	.active {
		background: linear-gradient(266.36deg, #f86476 0%, #fea185 34.16%);
		border-radius: 50px;
		height: $active-dot-height;
	}

	@include _tablet {
		padding-left: 50px;
		padding-right: 50px;
	}
}

// buttons mixins
@mixin btn(
	$font-size: 18px,
	$line-height: 22px,
	$background-color: $blue,
	$color: #fff,
	$hover-color: #fff,
	$hover-border-color: transparent,
	$hover-background: $hoverblue,
	$padding: 14px 115px,
	$border-color: transparent
) {
	display: flex;
	align-items: center;
	justify-content: center;
	border: none;
	outline: none;
	font-size: $font-size;
	line-height: $line-height;
	font-weight: bold;
	text-decoration: none;
	border-radius: 100px;
	border: 1px solid $border-color;
	width: fit-content;
	transition: 0.3s all;
	cursor: pointer;
	background-color: $background-color;
	color: $color;
	padding: $padding;
	@media (min-width: 1024px) {
		&:hover,
		&:focus {
			color: $hover-color;
			background-color: $hover-background;
			border-color: $hover-border-color;
		}
	}
}

// inputs mixins
@mixin input(
	$border-color: $grey2,
	$padding: 15px 40px 15px 30px,
	$svg-color: $grey2,
	$input-background: #fff,
	$border-radius: 50px,
	$caption-color: #000,
	$svg-position: 50%
) {
	position: relative;
	width: 100%;
	input,
	textarea {
		@extend %input-text;
		width: 100%;
		outline: none;
		border: 1px solid $border-color;
		border-radius: $border-radius;
		padding: $padding;
		resize: none;
		background-color: $input-background;
		&::placeholder {
			color: $grey1;
		}
	}
	svg {
		width: 14px;
		height: 14px;
		position: absolute;
		top: $svg-position;
		transform: translateY(-50%);
		right: 20px;
		color: $svg-color;
	}
	&-caption {
		display: block;
		font-weight: normal;
		color: $caption-color;
		font-size: 14px;
		line-height: 17px;
		margin-bottom: 5px;
	}
}

// checkbox mixins
@mixin checkbox($font-size: 14px, $line-height: 17px, $text-color: #000, $margin-bottom: 15px) {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	cursor: pointer;
	margin-bottom: $margin-bottom;
	input {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	.checkbox__text {
		font-weight: 300;
		font-size: $font-size;
		line-height: $line-height;
		color: $text-color;
	}
	input:checked ~ .checkmark::after {
		opacity: 1;
		visibility: visible;
	}
	input:checked ~ .checkbox__text {
		color: rgba(123, 123, 123, 0);
		font-weight: bold;
		background: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
		background-clip: text;
		color: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
	}
}
@mixin checkmark(
	$width: 13px,
	$height: 13px,
	$border-color: $grey1,
	$check-size: 13px 9px,
	$margin-right: 5px,
	$border-radius: 2px
) {
	width: $width;
	height: $height;
	border: 1px solid $border-color;
	margin-right: $margin-right;
	position: relative;
	border-radius: $border-radius;
	&::after {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-position: center center;
		background-image: url('../img/check2.svg');
		background-size: $check-size;
		opacity: 0;
		visibility: hidden;
		transition: 0.3s all;
	}
}

// checkbox-color mixins
@mixin checkbox-color($width: 30px, $height: 30px, $border-color: $blue2, $border-radius: 5px) {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	cursor: pointer;
	width: $width;
	height: $height;
	input {
		display: none;
		opacity: 0;
		visibility: hidden;
	}
	input:checked ~ .checkmark {
		border: 3px solid $border-color;
	}
	.checkmark {
		border-radius: $border-radius;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: 0.3s all;
		border: 3px solid transparent;
		width: 100%;
		height: 100%;
	}
}

// radio btns
@mixin radio-btn($font-size: 16px, $line-height: 19px, $text-color: $grey2, $checked-text-color: #000) {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	cursor: pointer;
	input {
		opacity: 0;
		visibility: hidden;
		display: none;
	}
	.radio-btn__text {
		font-weight: normal;
		font-size: $font-size;
		line-height: $line-height;
		color: $text-color;
		transition: 0.3s all;
	}

	input:checked ~ .radio-btn__text {
		font-weight: bold;
		color: $checked-text-color;
	}
	input:checked ~ .checkmark:after {
		opacity: 1;
		visibility: visible;
	}
}

@mixin checkmark-radio-btn(
	$width: 17px,
	$height: 17px,
	$border-radius: 50%,
	$background-color: #fff,
	$margin-right: 15px,
	$after-background-color: linear-gradient(266.36deg, #f86476 0%, #fea185 65.63%),
	$after-width: 9px,
	$after-height: 9px,
	$after-border-radius: 50%
) {
	width: $width;
	height: $height;
	border-radius: $border-radius;
	background-color: $background-color;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	margin-right: $margin-right;
	&::after {
		content: '';
		width: $after-width;
		height: $after-height;
		border-radius: $after-border-radius;
		background: $after-background-color;
		transition: 0.3s all;
		opacity: 0;
		visibility: hidden;
	}
}

// selects

@mixin select(
	$padding: 15px 25px,
	$border-radius: 25px,
	$border-radius-open: 0 0 25px 25px,
	$border: none,
	$background-color: #fff,
	$border-radius-main: 50px
) {
	width: 100%;
	position: relative;
	.ss-single-selected {
		padding: $padding;
		border: $border;
		border-radius: $border-radius-main;
		height: auto;
		margin: 0;
		background-color: $background-color;
	}
	.ss-open-below {
		border-radius: $border-radius !important;
		border-bottom-left-radius: 0 !important;
		border-bottom-right-radius: 0 !important;
	}
	.ss-open {
		border: none;
		border-radius: $border-radius-open;
		overflow: hidden;
		z-index: 9;
	}
	.ss-option {
		padding: $padding !important;
	}
	.ss-list {
		position: relative;
		z-index: 2;
	}
	.ss-arrow {
		.arrow-down,
		.arrow-up {
			border: none !important;
			width: 14px;
			height: 100%;
			justify-content: center;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 14px 7px;
			background-image: url('../img/arrow-select.svg');
		}
		.arrow-down {
			transform: rotate(0deg) !important;
		}
		.arrow-up {
			transform: rotate(180deg) !important;
		}
	}
}

// adaptiv fonts

@mixin adaptiv-font($pcSize, $mobSize, $maxWidth: 1500) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}
