.btn-upload {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 5px 8px;
	background: linear-gradient(266.36deg, #f86476 0%, #fea185 65.63%);
	border-radius: 50px;
	font-weight: bold;
	font-size: 14px;
	line-height: 17px;
	text-align: center;
	color: #ffffff;
	width: fit-content;
	margin-left: auto;
	margin-right: auto;
	cursor: pointer;
	svg {
		width: 10px;
		height: 10px;
		margin-right: 10px;
		transition: 0.3s all;
	}
	input {
		display: none;
		visibility: hidden;
		opacity: 0;
	}
	&:hover {
		svg {
			transform: translateY(3px);
		}
	}
}
