.loyalty {
	margin-bottom: $section-margin;
	&__section {
		padding: 40px;
		background-color: #ffffff;
		box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.05);
		border-radius: 25px;
		margin-bottom: #{$inner-margin + 10px};
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: #{$inner-margin - 20px};
		p {
			margin-bottom: 0;
		}
	}
	.readmore-js-toggle {
		padding: 13px 19px;
		max-width: 400px;
		display: flex !important;
		align-items: center;
		justify-content: space-between;
		margin-left: auto;
		margin-right: auto;
		background-color: $blue;
		border-radius: 10px;
		font-weight: bold;
		font-size: 14px;
		line-height: 15px;
		letter-spacing: 0.007px;
		text-decoration: none;
		color: #ffffff;
		position: relative;
		top: -30px;
		svg {
			width: 7px;
			height: 14px;
			color: #fff;
			transform: rotate(90deg);
		}
	}

	@include _mobile {
		&__section {
			position: relative;
			padding: 40px 15px;
			width: calc(100% + 30px);
			left: -15px;
		}
	}
}

.loyalty.text {
	p {
		@extend %secondary-text;
		margin-bottom: #{$inner-margin - 20px};
	}

	p.grey {
		font-weight: normal;
		font-size: 10px;
		line-height: 18px;
		letter-spacing: 0.007px;
		color: $grey2;
	}
	ul {
		@extend %ul-secondary;
	}
	.href {
		text-decoration: none;
		font-weight: bold;
		font-size: 14px;
		line-height: 15px;
		letter-spacing: 0.007px;
		color: $blue;
		&:hover {
			text-decoration: underline;
		}
	}
	.uppercase {
		text-transform: uppercase;
	}
}
