.blog-section {
	margin-bottom: $outter-margin;
	.owl-stage-outer {
		padding: 15px;
		left: -15px;
		width: calc(100% + 30px);
	}
	.owl-dots {
		margin-top: 20px;
	}

	@include _mobile {
		margin-bottom: $outter-margin_mobile;
		.owl-dots {
			margin-top: 10px;
		}
	}

	@include _tablet {
		margin-bottom: $outter-margin_tablet;
		.section-heading {
			justify-content: flex-start;
		}
	}
}
