.account-history {
	width: 100%;
	display: block;
	height: 400px;
	position: relative;
	.row {
		height: 400px;
		overflow-y: auto;
	}
	&::after {
		content: '';
		position: absolute;
		background: linear-gradient(180deg, #ffffff 10.2%, rgba(255, 255, 255, 0) 100%);
		transform: rotate(-180deg);
		height: 100px;
		bottom: 0;
		width: 100%;
	}
}
