.breadcrumbs {
	@extend %ul-reset;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: $inner-margin !important;
	margin-top: $inner-margin !important;

	li,
	li a {
		font-weight: normal !important;
		text-decoration: none;
		font-size: 20px !important;
		line-height: 24px !important;
		text-align: center;
		color: #000000;
	}
	.slash {
		font-weight: bold;
		margin: 0 5px;
	}
	li {
		margin-bottom: 0 !important;
		padding-left: 0 !important;
		&::before {
			content: none !important;
		}
	}
	&_left {
		justify-content: flex-start;
	}

	@include _mobile {
		margin-top: #{$inner-margin - 15px} !important;
		margin-bottom: #{$inner-margin - 15px} !important;
		white-space: nowrap;
		overflow-x: auto;
		width: 100%;
		display: inline-block;
		li {
			display: inline-block;
		}
	}
	@include _tablet {
		margin-top: 60px;
		margin-bottom: 20px;
	}
}
