.rating {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 13px;
		height: 13px;
		margin-right: 8px;
		svg {
			width: 100%;
			height: 100%;
		}
		&_big {
			width: 18px;
			height: 18px;
		}
	}
	&__caption {
		font-weight: 300;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		color: #000000;
		text-decoration: none;
	}
	&__reviews {
		font-weight: 300;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		text-decoration: underline;
		color: $grey2;
		margin-left: 8px;
		&:hover {
			text-decoration: none;
		}
	}

	@include _mobile {
		&__item {
			width: 9px;
			height: 9px;
			margin-right: 5px;
		}
	}
}
