.account-form {
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	max-width: 375px;
	fieldset {
		border: none;
		padding: 0;
		margin: 0;
	}
	.page-title {
		display: block;
		margin-bottom: $inner-margin;
	}
	&__notice {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		font-size: 16px;
		line-height: 19px;
		color: #000000;
	}
	&__notice .caption {
		font-weight: normal;
	}
	&__notice .href {
		font-weight: bold;
		text-decoration: underline;
		color: #000;
		&:hover {
			text-decoration: none;
		}
	}

	@include _mobile {
		.page-title {
			margin-bottom: #{$inner-margin - 20px};
		}
	}
}
