.description-delivery {
	&__item {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin-bottom: #{$inner-margin - 15px};
		svg {
			width: 23px;
			min-width: 23px;
			height: 23px;
			margin-right: 10px;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__text {
		font-weight: 300;
		font-size: 14px;
		line-height: 17px;
		color: #000000;

		b {
			font-weight: bold;
		}
	}
}

.description-delivery-select {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: #{$inner-margin - 15px};
	&__caption {
		font-weight: normal;
		font-size: 20px;
		line-height: 24px;
		color: #000000;

		margin-right: 15px;
	}
	&__select {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		width: fit-content;
		position: relative;
		.placeholder {
			color: rgba(123, 123, 123, 0);
			background: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
			background-clip: text;
			color: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
		}
		.ss-single-selected {
			padding: 15px 25px;
			border: none;
			height: auto;
			// padding: 0;
			margin: 0;
		}
		.ss-open-below {
			border-radius: 0;
		}
		.ss-open {
			border: none;
			border-radius: 0;
			overflow: hidden;
		}
		.ss-option {
			padding: 5px 15px !important;
		}
		.ss-list {
			position: relative;
			z-index: 123;
		}
		.ss-arrow {
			.arrow-down,
			.arrow-up {
				border: none !important;
				width: 14px;
				height: 100%;
				justify-content: center;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 7px 14px;
				background-image: url('../img/arrow.svg');
			}
			.arrow-down {
				transform: rotate(90deg) !important;
			}
			.arrow-up {
				transform: rotate(-90deg) !important;
			}
		}

		@include _mobile {
			.ss-single-selected {
				padding: 0;
			}

			.ss-open {
				width: 200px;
				right: 0;
			}
		}
	}

	@include _tablet {
		margin-bottom: 0;
	}
}
