.product-carousel {
	margin-bottom: $outter-margin;
	position: relative;
	&_grey {
		margin-bottom: 0;
		background-color: $grey3;
		padding: $outter-padding;
	}
	&_margin {
		margin-bottom: $outter-margin !important;
	}
	.product-card {
		border-right: 1px solid $white;
	}
	.owl-dots {
		margin-top: $inner-margin;
	}

	@include _mobile {
		margin-bottom: $outter-margin_mobile;
		&_grey {
			padding: $outter-padding_mobile;
			margin-bottom: 0;
		}
	}
}
