.gallery {
	position: sticky;
	top: 130px;
	margin-bottom: $outter-margin;

	@include _mobile {
		margin-bottom: $outter-margin_mobile;
	}
	@include _tablet {
		margin-bottom: $outter-margin_tablet;
	}
}
.gallery-big {
	position: relative;
	background-color: $grey3;
	margin-bottom: 2px;
	.stickers {
		position: absolute;
		top: 30px;
		left: 0;
		&__item {
			border-radius: 0 100px 100px 0;
			margin-bottom: #{$inner-margin / 2};
			&_yellow {
			}
		}
	}
	.actions {
		position: absolute;
		display: flex;
		align-items: center;
		flex-direction: column;
		top: 20px;
		right: 20px;
		z-index: 2;
		&__item {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 24px;
			height: 24px;
			margin-bottom: 10px;
			svg {
				width: 100%;
				height: 100%;
				color: $grey1;
				transition: 0.3s all;
			}
		}

		&:hover svg {
			color: $orange;
		}
	}
	.share {
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		color: #000000;
		text-decoration: none;
		position: absolute;
		z-index: 2;
		bottom: 25px;
		left: 40px;
		svg {
			margin-left: 10px;
			width: 14px;
			height: 14px;
			transition: 0.3s all;
		}

		&:hover,
		&:focus {
			svg {
				transform: scale(1.1);
			}
		}
	}
	&__img {
		width: 320px;
		height: 420px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-left: auto;
		margin-right: auto;
		img {
			width: 100%;
			height: 100%;
		}
	}

	@include _mobile {
		padding: 10px 0;
		&__img {
			height: 240px;
			width: auto;
			img {
				width: auto !important;
			}
		}
		.share {
			left: 10px;
			bottom: 5px;
		}
	}

	@include _tablet {
		&__img {
			height: 505px;
			width: auto;
			img {
				width: auto !important;
			}
		}
	}
}

.gallery-thumbs {
	&__img {
		cursor: pointer;
	}

	@include _mobile {
		&__img {
			height: 100px;
			img {
				height: 100%;
				object-fit: cover;
			}
		}
		.owl-dots {
			margin-top: 20px;
		}
	}

	@include _tablet {
		&__img {
			height: 100px;
		}
	}

	@include _laptop-sm {
		&__img {
			height: 80px;
			img {
				height: 100%;
				object-fit: cover;
			}
		}
	}
}
