.checkout-form {
	border-radius: 10px;
	background-color: $grey3;
	&__heading {
		padding: 30px 50px 30px 30px;
		background-color: $blue;
		position: relative;
		border-radius: 10px 10px 0 0;
		font-weight: 300;
		font-size: 20px;
		line-height: 24px;
		color: #ffffff;
		a {
			color: #fff;
			font-weight: bold;
		}
		b {
			display: block;
			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
			color: #ffffff;
			margin-top: #{$inner-margin - 15px};
		}
		svg {
			position: absolute;
			bottom: 0;
			width: 100px;
			height: 117px;
			right: 0;
			color: rgba(255, 255, 255, 0.1);
		}
	}
	fieldset {
		border: none;
		margin: 0;
		margin-bottom: #{$inner-margin - 20px};
		border-bottom: 1px solid $grey6;
		padding: 0 20px 0 20px;
		&:first-of-type {
			padding: 30px 30px 20px 30px;
		}
		&:last-of-type {
			border: none;
			margin-bottom: 0;
		}
	}
	&__title {
		font-weight: bold;
		font-size: 25px;
		line-height: 30px;
		color: #000000;
		display: block;
		margin-bottom: #{$inner-margin - 20px};
	}
	&__notice {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		text-align: center;
		color: #000000;
		max-width: 430px;
		margin-left: auto;
		margin-right: auto;
		padding-bottom: #{$inner-margin - 10px};
	}
	&__caption {
		display: block;
		font-weight: normal;
		color: #000;
		font-size: 14px;
		line-height: 17px;
		margin-bottom: 5px;
	}

	@include _mobile {
		fieldset {
			padding: 0 15px 0 15px;
			&:first-of-type {
				padding: 30px 15px 20px 15px;
			}
		}

		&__notice {
			margin-left: 30px;
			margin-right: 30px;
		}
	}

	@include _tablet {
		&__heading {
			padding-right: 300px;
		}
	}
}
