.account-form__input {
	@include input();
	margin-bottom: $inner-margin;
	@include _mobile {
		margin-bottom: 20px;
	}
	@include _tablet {
		margin-bottom: 20px;
	}
}

.account-data__input {
	@include input($svg-position: 47px, $border-color: $grey1);
	margin-bottom: $inner-margin;
	.change-password {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
		text-decoration: underline;
		color: $grey2;
		position: absolute;
		top: 0;
		right: 0;
		&:hover {
			text-decoration: none;
		}
	}

	@include _mobile {
		margin-bottom: #{$inner-margin - 10px};
	}
}

.checkout-form__input {
	@include input($border-color: transparent, $padding: 15px 20px);
	margin-bottom: #{$inner-margin / 2};
}

.checkout-form__textarea {
	@include input($border-color: transparent, $padding: 15px 20px, $border-radius: 10px);
	margin-bottom: #{$inner-margin - 10px};
}

.modal__input {
	@include input();
	margin-bottom: 25px;
}
