.header-categories {
	display: inline-block;
	vertical-align: middle;
	list-style: none;
	padding: 0;
	margin: 0;
	&__item {
		display: inline-flex;
		align-items: center;
		justify-content: flex-start;
		cursor: pointer;
		&_more {
		}
		&::after {
			content: '';
			width: 100%;
			position: absolute;
			height: calc(100% + 60px);
			background-color: transparent;
			left: 0;
			bottom: -60px;
			z-index: -1;
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
		}
	}
	&__item:first-child & {
		&__link {
			padding-left: 0;
		}
	}
	&__item:last-child & {
		&__link {
			padding-right: 0;
		}
	}
	&__item_more {
		&::after {
			bottom: -45px;
		}
	}
	&__link {
		padding: 0 20px;
		font-weight: 300;
		font-size: 18px;
		line-height: 22px;
		color: rgba(123, 123, 123, 1);
		text-decoration: none;
	}
	&__toggle {
		width: 12px;
		height: 6px;
		margin-bottom: 10px;
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-left: 9px;
		margin-top: 12px;
		transition: 0.3s all;
		position: relative;
		&_mobile {
			opacity: 0;
			visibility: hidden;
			display: none;
		}
		svg {
			width: 100%;
			height: 100%;
		}
	}
	&__menu-more {
		@extend %ul-reset;
		position: absolute;
		top: calc(100% + 40px);
		padding: 10px;
		background-color: #fff;
		border-radius: 10px;
		opacity: 0;
		visibility: hidden;
		left: 0;
		z-index: 30;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		a {
			text-decoration: none;
			font-size: 16px;
			color: #000;
			line-height: 18px;
			padding: 5px 0;
			display: flex;
			transition: 0.3s all;
			&:hover {
				color: $orange;
			}
		}
	}

	@include _mobile-tablet {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-direction: column;
		padding-left: 50px;
		&__item {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: flex-start;
		}
		&__link {
			padding-left: 0;
			width: 100%;
			padding-bottom: 15px;
		}
		&__toggle {
			display: flex;
			align-items: center;
			justify-content: center;
			opacity: 1;
			visibility: visible;
			margin: 0;
			min-width: 20px;
			width: 20px;
			height: 20px;
			svg {
				width: 7px;
				height: 14px;
				color: #ddd;
			}
		}
	}

	@include _mobile {
		padding-left: 15px;
	}

	@include _laptop-sm {
		&__link {
			padding: 0 10px;
		}
	}

	@include _laptop {
		&__link {
			padding: 0 10px;
		}
	}

	@media (min-width: 1200px) and(max-width: 1900px) {
		&__item_more::after {
			bottom: -50px !important;
		}
	}

	@media (min-width: 1024px) {
		&__item {
			&:hover,
			&:focus {
				.header-categories__menu-more {
					opacity: 1;
					visibility: visible;
				}
				.header-categories__toggle {
					transform: rotate(180deg);
				}
				.header-categories__link {
					color: rgba(123, 123, 123, 0);
					background: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
					background-clip: text;
					color: linear-gradient(180deg, rgba(254, 161, 133, 1) 0%, rgba(248, 100, 118, 1) 100%);
				}
				&:after {
					opacity: 1;
					visibility: visible;
					pointer-events: inherit;
				}
			}
		}
	}
}
#more-nav {
	@extend %ul-reset;
}
#nav {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	justify-content: flex-start;
	white-space: nowrap;
	position: relative;
	z-index: 1;
}
#more-nav {
	padding-left: 20px;
	padding-right: 20px;
	.submenu {
		display: none;
	}
	@include _mobile-tablet {
		display: none;
	}
}
