.hero {
	background: linear-gradient(86deg, #f4efec 0%, #f0d5d2 100%);
	overflow: hidden;
	position: relative;
	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.owl-nav {
		left: 100px;
		width: calc(100% - 200px);
		button svg {
			color: #fff;
		}
	}
	.owl-dots {
		position: absolute;
		bottom: 30px;
		left: 50%;
		transform: translateX(-50%);
		.owl-dot {
			background-color: #fff !important;
			opacity: 0.5;
		}
		.active {
			opacity: 1;
		}
	}
	&_grey {
		background: $grey4;
		margin-bottom: $section-margin;
	}
	&_small {
		.owl-nav,
		.owl-dots {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
	}
	@include _mobile-tablet {
		.owl-dots {
			.owl-dot {
				background-color: rgb(0, 0, 0) !important;
				opacity: 0.5;
			}
			.active {
				opacity: 1;
			}
		}
	}
	@include _mobile {
		margin-bottom: $inner-margin;
	}
	@include _tablet {
		margin-bottom: #{$inner-margin + 10px};
	}
}
