html {
	box-sizing: border-box;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
	font-family: $primary-font, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
		'Open Sans', 'Helvetica Neue', sans-serif;
}
.clearfix::after {
	content: '';
	display: table;
	clear: both;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
	margin: 0;
	padding: 0;
}

button {
	background-color: transparent;
	border: none;
	cursor: pointer;
}

